import axios from 'axios';

const ServerAddress = 'https://open.auxgroup.com/api/auxhr/meetingrate/participants/'
//const ServerAddress = 'http://localhost:9999/participants/'

export default {

  meetings(id){
    return axios.get(ServerAddress+'meetings/'+id);
  },

}


