<template>

  <main id="content" class="content">

    <div class="content-wrap" >

      <h2 class="page-title">心得体会列表</h2>

      <div class="row" v-for="item in partiList">

        <section class="widget">
          <header>
            <h6>
              <span class="fw-semi-bold">Styles</span>
            </h6>
            <div class="widget-controls">
              <a href="#"> <i class="glyphicon glyphicon-cog"></i>
              </a>
              <a data-widgster="close" href="#"> <i class="glyphicon glyphicon-remove"></i>
              </a>
            </div>
          </header>
          <div class="widget-body">
            <h3>
              Stripped
              <span class="fw-semi-bold">Table</span>
            </h3>

            <div v-html="item.meetingmsg"></div>

          </div>
        </section>

      </div>

    </div>

  </main>

</template>

<script>

  import '../styles/deskapp.scss'
  
  import records from '../scripts/meetingRecords.js'
  

  export default {

      data() {
            return {
              browser : 'no',
              partiList:[],
          }
      },


      mounted(){
   
       var this2 = this;

       records.meetings('8a829be75c67565a015c6764044c0002').then(
        function(response) { 
          console.log(response.data);
            this2.partiList = response.data;
        });
      },


      computed: {
        
        isWeiXin : function () {
          var ua = window.navigator.userAgent.toLowerCase();
      
          if(ua.match(/MicroMessenger/i) == 'micromessenger'){
            return true;
          }else{
            return false;
          }
        },
      },

  }

</script>

<style scoped>

  .content{
      padding-top:20px;
  }

  .content-wrap{
    position: inherit;
  }
  
  .page-controls {
    left:0;
    right:0;
    margin-left: 0px;
  }

</style>


