ult;\n\n\n// Z-index master list\n//\n// Warning: Avoid customizing these values. They're used for a bird's eye view\n// of components dependent on the z-axis and are designed to all work together.\n\n$zindex-navbar:            1000 !default;\n$zindex-dropdown:          1000 !default;\n$zindex-popover:           1060 !default;\n$zindex-tooltip:           1070 !default;\n$zindex-navbar-fixed:      1030 !default;\n$zindex-navbar-sticky:     1030 !default;\n$zindex-modal-bg:          1040 !default;\n$zindex-modal:             1050 !default;\n\n\n// Navbar\n\n$navbar-border-radius:              $border-radius !default;\n$navbar-padding-horizontal:         $spacer !default;\n$navbar-padding-vertical:           ($spacer / 2) !default;\n\n$navbar-dark-color:                 rgba(255,255,255,.5) !default;\n$navbar-dark-hover-color:           rgba(255,255,255,.75) !default;\n$navbar-dark-active-color:          rgba(255,255,255,1) !default;\n$navbar-dark-disabled-color:        rgba(255,255,255,.25) !default;\n\n$navbar-light-color:                rgba(0,0,0,.3) !default;\n$navbar-light-hover-color:          rgba(0,0,0,.6) !default;\n$navbar-light-active-color:         rgba(0,0,0,.8) !default;\n$navbar-light-disabled-color:       rgba(0,0,0,.15) !default;\n\n\n// Navs\n\n$nav-link-padding:                          .5em 1em !default;\n$nav-link-hover-bg:                         $gray-lighter !default;\n\n$nav-disabled-link-color:                   $gray-light !default;\n$nav-disabled-link-hover-color:             $gray-light !default;\n\n$nav-tabs-border-color:                     #ddd !default;\n\n$nav-tabs-link-border-width:                $border-width !default;\n$nav-tabs-link-hover-border-color:          $gray-lighter !default;\n\n$nav-tabs-active-link-hover-bg:             $body-bg !default;\n$nav-tabs-active-link-hover-color:          $gray !default;\n$nav-tabs-active-link-hover-border-color:   #ddd !default;\n\n$nav-tabs-justified-link-border-color:            #ddd !default;\n$nav-tabs-justified-active-link-border-color:     $body-bg !default;\n\n$nav-pills-border-radius:                   $border-radius !default;\n$nav-pills-active-link-hover-bg:            $component-active-bg !default;\n$nav-pills-active-link-hover-color:         $component-active-color !default;\n\n\n// Pagination\n\n$pagination-padding-x:                .75rem !default;\n$pagination-padding-y:                .5rem !default;\n$pagination-padding-x-sm:             .75rem !default;\n$pagination-padding-y-sm:             .275rem !default;\n$pagination-padding-x-lg:             1.5rem !default;\n$pagination-padding-y-lg:             .75rem !default;\n\n\n$pagination-color:                     $link-color !default;\n$pagination-bg:                        #fff !default;\n$pagination-border-width:              $border-width !default;\n$pagination-border-color:              #ddd !default;\n\n$pagination-hover-color:               $link-hover-color !default;\n$pagination-hover-bg:                  $gray-lighter !default;\n$pagination-hover-border:              #ddd !default;\n\n$pagination-active-color:              #fff !default;\n$pagination-active-bg:                 $brand-primary !default;\n$pagination-active-border:             $brand-primary !default;\n\n$pagination-disabled-color:            $gray-light !default;\n$pagination-disabled-bg:               #fff !default;\n$pagination-disabled-border:           #ddd !default;\n\n\n// Pager\n\n$pager-bg:                             $pagination-bg !default;\n$pager-border-width:                   $border-width !default;\n$pager-border-color:                   $pagination-border-color !default;\n$pager-border-radius:                  15px !default;\n\n$pager-hover-bg:                       $pagination-hover-bg !default;\n\n$pager-active-bg:                      $pagination-active-bg !default;\n$pager-active-color:                   $pagination-active-color !default;\n\n$pager-disabled-color:                 $pagination-disabled-color !default;\n\n\n// Jumbotron\n\n$jumbotron-padding:              2rem !default;\n$jumbotron-bg:                   $gray-lighter !default;\n\n\n// Form states and alerts\n//\n// Define colors for form feedback states and, by default, alerts.\n\n$state-success-text:             #3c763d !default;\n$state-success-bg:               #dff0d8 !default;\n$state-success-border:           darken($state-success-bg, 5%) !default;\n\n$state-info-text:                #31708f !default;\n$state-info-bg:                  #d9edf7 !default;\n$state-info-border:              darken($state-info-bg, 7%) !default;\n\n$state-warning-text:             #8a6d3b !default;\n$state-warning-bg:               #fcf8e3 !default;\n$state-warning-border:           darken($state-warning-bg, 5%) !default;\n\n$state-danger-text:              #a94442 !default;\n$state-danger-bg:                #f2dede !default;\n$state-danger-border:            darken($state-danger-bg, 5%) !default;\n\n\n// Cards\n$card-spacer-x:            1.25rem !default;\n$card-spacer-y:            .75rem !default;\n$card-border-width:        1px !default;\n$card-border-radius:       $border-radius !default;\n$card-border-color:        #e5e5e5 !default;\n$card-border-radius-inner: $card-border-radius !default;\n$card-cap-bg:              #f5f5f5 !default;\n$card-bg:                  #fff !default;\n\n$card-link-hover-color:    #fff !default;\n\n\n// Tooltips\n\n$tooltip-max-width:           200px !default;\n$tooltip-color:               #fff !default;\n$tooltip-bg:                  #000 !default;\n$tooltip-opacity:             .9 !default;\n\n$tooltip-arrow-width:         5px !default;\n$tooltip-arrow-color:         $tooltip-bg !default;\n\n\n// Popovers\n\n$popover-bg:                          #fff !default;\n$popover-max-width:                   276px !default;\n$popover-border-width:                $border-width !default;\n$popover-border-color:                rgba(0,0,0,.2) !default;\n\n$popover-title-bg:                    darken($popover-bg, 3%) !default;\n\n$popover-arrow-width:                 10px !default;\n$popover-arrow-color:                 $popover-bg !default;\n\n$popover-arrow-outer-width:           ($popover-arrow-width + 1) !default;\n$popover-arrow-outer-color:           fade-in($popover-border-color, 0.05) !default;\n\n\n// Labels\n\n$label-default-bg:            $gray-light !default;\n$label-primary-bg:            $brand-primary !default;\n$label-success-bg:            $brand-success !default;\n$label-info-bg:               $brand-info !default;\n$label-warning-bg:            $brand-warning !default;\n$label-danger-bg:             $brand-danger !default;\n\n$label-color:                 #fff !default;\n$label-link-hover-color:      #fff !default;\n$label-font-weight:           bold !default;\n\n\n// Modals\n\n// Padding applied to the modal body\n$modal-inner-padding:         15px !default;\n\n$modal-title-padding:         15px !default;\n$modal-title-line-height:     $line-height !default;\n\n$modal-content-bg:                             #fff !default;\n$modal-content-border-color:                   rgba(0,0,0,.2) !default;\n\n$modal-backdrop-bg:           #000 !default;\n$modal-backdrop-opacity:      .5 !default;\n$modal-header-border-color:   #e5e5e5 !default;\n$modal-footer-border-color:   $modal-header-border-color !default;\n\n$modal-lg:                    900px !default;\n$modal-md:                    600px !default;\n$modal-sm:                    300px !default;\n\n\n// Alerts\n//\n// Define alert colors, border radius, and padding.\n\n$alert-padding:               15px !default;\n$alert-border-radius:         $border-radius !default;\n$alert-link-font-weight:      bold !default;\n$alert-border-width:          $border-width !default;\n\n$alert-success-bg:            $state-success-bg !default;\n$alert-success-text:          $state-success-text !default;\n$alert-success-border:        $state-success-border !default;\n\n$alert-info-bg:               $state-info-bg !default;\n$alert-info-text:             $state-info-text !default;\n$alert-info-border:           $state-info-border !default;\n\n$alert-warning-bg:            $state-warning-bg !default;\n$alert-warning-text:          $state-warning-text !default;\n$alert-warning-border:        $state-warning-border !default;\n\n$alert-danger-bg:             $state-danger-bg !default;\n$alert-danger-text:           $state-danger-text !default;\n$alert-danger-border:         $state-danger-border !default;\n\n\n// Progress bars\n\n$progress-bg:                 #f5f5f5 !default;\n$progress-bar-color:          #fff !default;\n$progress-border-radius:      $border-radius !default;\n\n$progress-bar-bg:             $brand-primary !default;\n$progress-bar-success-bg:     $brand-success !default;\n$progress-bar-warning-bg:     $brand-warning !default;\n$progress-bar-danger-bg:      $brand-danger !default;\n$progress-bar-info-bg:        $brand-info !default;\n\n\n// List group\n\n$list-group-bg:                 #fff !default;\n$list-group-border-color:       #ddd !default;\n$list-group-border-width:       $border-width !default;\n$list-group-border-radius:      $border-radius !default;\n\n$list-group-hover-bg:           #f5f5f5 !default;\n$list-group-active-color:       $component-active-color !default;\n$list-group-active-bg:          $component-active-bg !default;\n$list-group-active-border:      $list-group-active-bg !default;\n$list-group-active-text-color:  lighten($list-group-active-bg, 40%) !default;\n\n$list-group-disabled-color:      $gray-light !default;\n$list-group-disabled-bg:         $gray-lighter !default;\n$list-group-disabled-text-color: $list-group-disabled-color !default;\n\n$list-group-link-color:         #555 !default;\n$list-group-link-hover-color:   $list-group-link-color !default;\n$list-group-link-heading-color: #333 !default;\n\n\n// Image thumbnails\n\n$thumbnail-padding:           .25rem !default;\n$thumbnail-bg:                $body-bg !default;\n$thumbnail-border-width:      $border-width !default;\n$thumbnail-border-color:      #ddd !default;\n$thumbnail-border-radius:     $border-radius !default;\n\n\n// Breadcrumbs\n\n$breadcrumb-padding-vertical:   .75rem !default;\n$breadcrumb-padding-horizontal: 1rem !default;\n\n$breadcrumb-bg:                 $gray-lighter !default;\n$breadcrumb-divider-color:      $gray-light !default;\n$breadcrumb-active-color:       $gray-light !default;\n$breadcrumb-divider:            \"/\" !default;\n\n\n// Carousel\n\n$carousel-text-shadow:                        0 1px 2px rgba(0,0,0,.6) !default;\n\n$carousel-control-color:                      #fff !default;\n$carousel-control-width:                      15% !default;\n$carousel-control-opacity:                    .5 !default;\n$carousel-control-font-size:                  20px !default;\n\n$carousel-indicator-active-bg:                #fff !default;\n$carousel-indicator-border-color:             #fff !default;\n\n$carousel-caption-color:                      #fff !default;\n\n\n// Close\n\n$close-font-weight:           bold !default;\n$close-color:                 #000 !default;\n$close-text-shadow:           0 1px 0 #fff !default;\n\n\n// Code\n\n$code-color:                  #bd4147 !default;\n$code-bg:                     #f7f7f9 !default;\n\n$kbd-color:                   #fff !default;\n$kbd-bg:                      #333 !default;\n\n$pre-bg:                      #f7f7f9 !default;\n$pre-color:                   $gray-dark !default;\n$pre-border-color:            #ccc !default;\n$pre-scrollable-max-height:   340px !default;\n","@mixin hover {\n  @if $enable-hover-media-query {\n    // See Media Queries Level 4: http://drafts.csswg.org/mediaqueries/#hover\n    // Currently shimmed by https://github.com/twbs/mq4-hover-shim\n    @media (hover: hover) {\n      &:hover { @content }\n    }\n  }\n  @else {\n    &:hover { @content }\n  }\n}\n\n@mixin hover-focus {\n  @if $enable-hover-media-query {\n    &:focus { @content }\n    @include hover { @content }\n  }\n  @else {\n    &:focus,\n    &:hover {\n      @content\n    }\n  }\n}\n\n@mixin plain-hover-focus {\n  @if $enable-hover-media-query {\n    &,\n    &:focus {\n      @content\n    }\n    @include hover { @content }\n  }\n  @else {\n    &,\n    &:focus,\n    &:hover {\n      @content\n    }\n  }\n}\n\n@mixin hover-focus-active {\n  @if $enable-hover-media-query {\n    &:focus,\n    &:active {\n      @content\n    }\n    @include hover { @content }\n  }\n  @else {\n    &:focus,\n    &:active,\n    &:hover {\n      @content\n    }\n  }\n}\n","// WebKit-style focus\n\n@mixin tab-focus() {\n  // Default\n  outline: thin dotted;\n  // WebKit\n  outline: 5px auto -webkit-focus-ring-color;\n  outline-offset: -2px;\n}\n","//\n// Headings\n//\n\nh1, h2, h3, h4, h5, h6,\n.h1, .h2, .h3, .h4, .h5, .h6 {\n  margin-bottom: $headings-margin-bottom;\n  font-family: $headings-font-family;\n  font-weight: $headings-font-weight;\n  line-height: $headings-line-height;\n  color: $headings-color;\n}\n\nh1 { font-size: $font-size-h1; }\nh2 { font-size: $font-size-h2; }\nh3 { font-size: $font-size-h3; }\nh4 { font-size: $font-size-h4; }\nh5 { font-size: $font-size-h5; }\nh6 { font-size: $font-size-h6; }\n\n// These declarations are kept separate from and placed after\n// the previous tag-based declarations so that the classes beat the tags in\n// the CSS cascade, and thus <h1 class=\"h2\"> will be styled like an h2.\n.h1 { font-size: $font-size-h1; }\n.h2 { font-size: $font-size-h2; }\n.h3 { font-size: $font-size-h3; }\n.h4 { font-size: $font-size-h4; }\n.h5 { font-size: $font-size-h5; }\n.h6 { font-size: $font-size-h6; }\n\n.lead {\n  font-size: $lead-font-size;\n  font-weight: $lead-font-weight;\n}\n\n// Type display classes\n.display-1 {\n  font-size: $display1-size;\n  font-weight: $display1-weight;\n}\n.display-2 {\n  font-size: $display2-size;\n  font-weight: $display2-weight;\n}\n.display-3 {\n  font-size: $display3-size;\n  font-weight: $display3-weight;\n}\n.display-4 {\n  font-size: $display4-size;\n  font-weight: $display4-weight;\n}\n\n\n//\n// Horizontal rules\n//\n\nhr {\n  margin-top: $spacer-y;\n  margin-bottom: $spacer-y;\n  border: 0;\n  border-top: $hr-border-width solid $hr-border-color;\n}\n\n\n//\n// Emphasis\n//\n\nsmall,\n.small {\n  font-size: 80%;\n  font-weight: normal;\n}\n\nmark,\n.mark {\n  padding: .2em;\n  background-color: $state-warning-bg;\n}\n\n\n//\n// Lists\n//\n\n.list-unstyled {\n  @include list-unstyled;\n}\n\n// Inline turns list items into inline-block\n.list-inline {\n  @include list-unstyled;\n}\n.list-inline-item {\n  display: inline-block;\n\n  &:not(:last-child) {\n    margin-right: $list-inline-padding;\n  }\n}\n\n// Horizontal description lists w/ grid classes\n.dl-horizontal {\n  margin-right: -$grid-gutter-width;\n  margin-left: -$grid-gutter-width;\n  @include clearfix;\n}\n\n\n//\n// Misc\n//\n\n// Builds on `abbr`\n.initialism {\n  font-size: 90%;\n  text-transform: uppercase;\n}\n\n// Blockquotes\n.blockquote {\n  padding: ($spacer / 2) $spacer;\n  margin-bottom: $spacer;\n  font-size: $blockquote-font-size;\n  border-left: .25rem solid $blockquote-border-color;\n}\n\n.blockquote-footer {\n  display: block;\n  font-size: 80%; // back to default font-size\n  line-height: $line-height;\n  color: $blockquote-small-color;\n\n  &::before {\n    content: \"\\2014 \\00A0\"; // em dash, nbsp\n  }\n}\n\n// Opposite alignment of blockquote\n.blockquote-reverse {\n  padding-right: $spacer;\n  padding-left: 0;\n  text-align: right;\n  border-right: .25rem solid $blockquote-border-color;\n  border-left: 0;\n}\n\n.blockquote-reverse .blockquote-footer {\n  &::before {\n    content: \"\";\n  }\n  &::after {\n    content: \"\\00A0 \\2014\"; // nbsp, em dash\n  }\n}\n","// Lists\n\n// Unstyled keeps list items block level, just removes default browser padding and list-style\n@mixin list-unstyled {\n  padding-left: 0;\n  list-style: none;\n}\n","@mixin clearfix() {\n  &::after {\n    content: \"\";\n    display: table;\n    clear: both;\n  }\n}\n","// Responsive images (ensure images don't scale beyond their parents)\n//\n// This is purposefully opt-in via an explicit class rather than being the default for all `<img>`s.\n// We previously tried the \"images are responsive by default\" approach in Bootstrap v2,\n// and abandoned it in Bootstrap v3 because it breaks lots of third-party widgets (including Google Maps)\n// which weren't expecting the images within themselves to be involuntarily resized.\n// See also https://github.com/twbs/bootstrap/issues/18178\n.img-fluid {\n  @include img-fluid();\n}\n\n// Rounded corners\n.img-rounded {\n  @include border-radius($border-radius-lg);\n}\n\n// Image thumbnails\n.img-thumbnail {\n  padding: $thumbnail-padding;\n  line-height: $line-height;\n  background-color: $thumbnail-bg;\n  border: $thumbnail-border-width solid $thumbnail-border-color;\n  border-radius: $thumbnail-border-radius;\n  transition: all .2s ease-in-out;\n  @include box-shadow(0 1px 2px rgba(0,0,0,.075));\n\n  // Keep them at most 100% wide\n  @include img-fluid(inline-block);\n}\n\n// Perfect circle\n.img-circle {\n  border-radius: 50%;\n}\n\n//\n// Figures\n//\n\n.figure {\n  // Ensures the caption's text aligns with the image.\n  display: inline-block;\n}\n\n.figure-img {\n  margin-bottom: ($spacer-y / 2);\n  line-height: 1;\n}\n\n.figure-caption {\n  font-size: 90%;\n  color: $gray-light;\n}\n","// Image Mixins\n// - Responsive image\n// - Retina image\n\n\n// Responsive image\n//\n// Keep images from scaling beyond the width of their parents.\n\n@mixin img-fluid($display: block) {\n  display: $display;\n  max-width: 100%; // Part 1: Set a maximum relative to the parent\n  height: auto; // Part 2: Scale the height according to the width, otherwise you get stretching\n}\n\n\n// Retina image\n//\n// Short retina mixin for setting background-image and -size.\n\n@mixin img-retina($file-1x, $file-2x, $width-1x, $height-1x) {\n  background-image: url($file-1x);\n\n  // Autoprefixer takes care of adding -webkit-min-device-pixel-ratio and -o-min-device-pixel-ratio,\n  // but doesn't convert dppx=>dpi.\n  // There's no such thing as unprefixed min-device-pixel-ratio since it's nonstandard.\n  // Compatibility info: http://caniuse.com/#feat=css-media-resolution\n  @media\n  only screen and (min-resolution: 192dpi), // IE9-11 don't support dppx\n  only screen and (min-resolution: 2dppx) { // Standardized\n    background-image: url($file-2x);\n    background-size: $width-1x $height-1x;\n  }\n}\n","// Single side border-radius\n\n@mixin border-radius($radius: $border-radius) {\n  @if $enable-rounded {\n    border-radius: $radius;\n  }\n}\n\n@mixin border-top-radius($radius) {\n  @if $enable-rounded {\n    border-top-right-radius: $radius;\n    border-top-left-radius: $radius;\n  }\n}\n\n@mixin border-right-radius($radius) {\n  @if $enable-rounded {\n    border-bottom-right-radius: $radius;\n    border-top-right-radius: $radius;\n  }\n}\n\n@mixin border-bottom-radius($radius) {\n  @if $enable-rounded {\n    border-bottom-right-radius: $radius;\n    border-bottom-left-radius: $radius;\n  }\n}\n\n@mixin border-left-radius($radius) {\n  @if $enable-rounded {\n    border-bottom-left-radius: $radius;\n    border-top-left-radius: $radius;\n  }\n}\n","// Toggles\n//\n// Used in conjunction with global variables to enable certain theme features.\n\n@mixin box-shadow($shadow...) {\n  @if $enable-shadows {\n    box-shadow: $shadow;\n  }\n}\n\n@mixin transition($transition...) {\n  @if $enable-transitions {\n    transition: $transition;\n  }\n}\n\n// Utilities\n@import \"mixins/breakpoints\";\n@import \"mixins/hover\";\n@import \"mixins/image\";\n@import \"mixins/label\";\n@import \"mixins/reset-filter\";\n@import \"mixins/resize\";\n@import \"mixins/screen-reader\";\n@import \"mixins/size\";\n@import \"mixins/tab-focus\";\n@import \"mixins/reset-text\";\n@import \"mixins/text-emphasis\";\n@import \"mixins/text-hide\";\n@import \"mixins/text-truncate\";\n\n// // Components\n@import \"mixins/alert\";\n@import \"mixins/buttons\";\n@import \"mixins/cards\";\n@import \"mixins/pagination\";\n@import \"mixins/lists\";\n@import \"mixins/list-group\";\n@import \"mixins/nav-divider\";\n@import \"mixins/forms\";\n@import \"mixins/progress\";\n@import \"mixins/table-row\";\n\n// // Skins\n@import \"mixins/background-variant\";\n@import \"mixins/border-radius\";\n@import \"mixins/gradients\";\n\n// // Layout\n@import \"mixins/clearfix\";\n@import \"mixins/center-block\";\n// @import \"mixins/navbar-align\";\n@import \"mixins/grid-framework\";\n@import \"mixins/grid\";\n@import \"mixins/pulls\";\n","// Inline and block code styles\ncode,\nkbd,\npre,\nsamp {\n  font-family: $font-family-monospace;\n}\n\n// Inline code\ncode {\n  padding: .2rem .4rem;\n  font-size: 90%;\n  color: $code-color;\n  background-color: $code-bg;\n  @include border-radius($border-radius);\n}\n\n// User input typically entered via keyboard\nkbd {\n  padding: .2rem .4rem;\n  font-size: 90%;\n  color: $kbd-color;\n  background-color: $kbd-bg;\n  @include border-radius($border-radius-sm);\n  @include box-shadow(inset 0 -.1rem 0 rgba(0,0,0,.25));\n\n  kbd {\n    padding: 0;\n    font-size: 100%;\n    font-weight: $nested-kbd-font-weight;\n    @include box-shadow(none);\n  }\n}\n\n// Blocks of code\npre {\n  display: block;\n  margin-top: 0;\n  margin-bottom: 1rem;\n  font-size: 90%;\n  line-height: $line-height;\n  color: $pre-color;\n\n  // Account for some code outputs that place code tags in pre tags\n  code {\n    padding: 0;\n    font-size: inherit;\n    color: inherit;\n    background-color: transparent;\n    border-radius: 0;\n  }\n}\n\n// Enable scrollable blocks of code\n.pre-scrollable {\n  max-height: $pre-scrollable-max-height;\n  overflow-y: scroll;\n}\n","// Container widths\n//\n// Set the container width, and override it for fixed navbars in media queries.\n\n.container {\n  @include make-container();\n  @include make-container-max-widths();\n\n}\n\n\n// Fluid container\n//\n// Utilizes the mixin meant for fixed width containers, but without any defined\n// width for fluid, full width layouts.\n\n.container-fluid {\n  @include make-container();\n}\n\n\n// Row\n//\n// Rows contain and clear the floats of your columns.\n\n@if $enable-grid-classes {\n  .row {\n    @include make-row();\n  }\n}\n\n\n// Columns\n//\n// Common styles for small and large grid columns\n\n@if $enable-grid-classes {\n  @include make-grid-columns();\n}\n\n\n// Flex variation\n//\n// Custom styles for additional flex alignment options.\n\n@if $enable-flex and $enable-grid-classes {\n\n  // Flex column reordering\n\n  @each $breakpoint in map-keys($grid-breakpoints) {\n    @include media-breakpoint-up($breakpoint) {\n      .col-#{$breakpoint}-first { order: -1; }\n      .col-#{$breakpoint}-last  { order: 1; }\n    }\n  }\n\n  // Alignment for every column in row\n\n  @each $breakpoint in map-keys($grid-breakpoints) {\n    @include media-breakpoint-up($breakpoint) {\n      .row-#{$breakpoint}-top    { align-items: flex-start; }\n      .row-#{$breakpoint}-center { align-items: center; }\n      .row-#{$breakpoint}-bottom { align-items: flex-end; }\n    }\n  }\n\n  // Alignment per column\n\n  @each $breakpoint in map-keys($grid-breakpoints) {\n    @include media-breakpoint-up($breakpoint) {\n      .col-#{$breakpoint}-top    { align-self: flex-start; }\n      .col-#{$breakpoint}-center { align-self: center; }\n      .col-#{$breakpoint}-bottom { align-self: flex-end; }\n    }\n  }\n}\n","/// Grid system\n//\n// Generate semantic grid columns with these mixins.\n\n@mixin make-container($gutter: $grid-gutter-width) {\n  margin-left: auto;\n  margin-right: auto;\n  padding-left:  ($gutter / 2);\n  padding-right: ($gutter / 2);\n  @if not $enable-flex {\n    @include clearfix();\n  }\n}\n\n\n// For each breakpoint, define the maximum width of the container in a media query\n@mixin make-container-max-widths($max-widths: $container-max-widths) {\n  @each $breakpoint, $container-max-width in $max-widths {\n    @include media-breakpoint-up($breakpoint) {\n      max-width: $container-max-width;\n    }\n  }\n}\n\n@mixin make-row($gutter: $grid-gutter-width) {\n  @if $enable-flex {\n    display: flex;\n    flex-wrap: wrap;\n  } @else {\n    @include clearfix();\n  }\n  margin-left:  ($gutter / -2);\n  margin-right: ($gutter / -2);\n}\n\n@mixin make-col($gutter: $grid-gutter-width) {\n  position: relative;\n  @if not $enable-flex {\n    float: left;\n  }\n  min-height: 1px;\n  padding-left:  ($gutter / 2);\n  padding-right: ($gutter / 2);\n}\n\n@mixin make-col-span($size, $columns: $grid-columns) {\n  @if $enable-flex {\n    flex: 0 0 percentage($size / $columns);\n  } @else {\n    width: percentage($size / $columns);\n  }\n}\n\n@mixin make-col-offset($size, $columns: $grid-columns) {\n  margin-left: percentage($size / $columns);\n}\n\n@mixin make-col-push($size, $columns: $grid-columns) {\n  left: if($size > 0, percentage($size / $columns), auto);\n}\n\n@mixin make-col-pull($size, $columns: $grid-columns) {\n  right: if($size > 0, percentage($size / $columns), auto);\n}\n\n@mixin make-col-modifier($type, $size, $columns) {\n  // Work around the lack of dynamic mixin @include support (https://github.com/sass/sass/issues/626)\n  @if $type == push {\n    @include make-col-push($size, $columns);\n  } @else if $type == pull {\n    @include make-col-pull($size, $columns);\n  } @else if $type == offset {\n    @include make-col-offset($size, $columns);\n  }\n}\n","// Breakpoint viewport sizes and media queries.\n//\n// Breakpoints are defined as a map of (name: minimum width), order from small to large:\n//\n//    (xs: 0, sm: 544px, md: 768px)\n//\n// The map defined in the `$grid-breakpoints` global variable is used as the `$breakpoints` argument by default.\n\n// Name of the next breakpoint, or null for the last breakpoint.\n//\n//    >> breakpoint-next(sm)\n//    md\n//    >> breakpoint-next(sm, (xs: 0, sm: 544px, md: 768px))\n//    md\n//    >> breakpoint-next(sm, $breakpoint-names: (xs sm md))\n//    md\n@function breakpoint-next($name, $breakpoints: $grid-breakpoints, $breakpoint-names: map-keys($breakpoints)) {\n  $n: index($breakpoint-names, $name);\n  @return if($n < length($breakpoint-names), nth($breakpoint-names, $n + 1), null);\n}\n\n// Minimum breakpoint width. Null for the smallest (first) breakpoint.\n//\n//    >> breakpoint-min(sm, (xs: 0, sm: 544px, md: 768px))\n//    544px\n@function breakpoint-min($name, $breakpoints: $grid-breakpoints) {\n  $min: map-get($breakpoints, $name);\n  @return if($min != 0, $min, null);\n}\n\n// Maximum breakpoint width. Null for the largest (last) breakpoint.\n// The maximum value is calculated as the minimum of the next one less 0.1.\n//\n//    >> breakpoint-max(sm, (xs: 0, sm: 544px, md: 768px))\n//    767px\n@function breakpoint-max($name, $breakpoints: $grid-breakpoints) {\n  $next: breakpoint-next($name, $breakpoints);\n  @return if($next, breakpoint-min($next, $breakpoints) - 1px, null);\n}\n\n// Media of at least the minimum breakpoint width. No query for the smallest breakpoint.\n// Makes the @content apply to the given breakpoint and wider.\n@mixin media-breakpoint-up($name, $breakpoints: $grid-breakpoints) {\n  $min: breakpoint-min($name, $breakpoints);\n  @if $min {\n    @media (min-width: $min) {\n      @content;\n    }\n  } @else {\n    @content;\n  }\n}\n\n// Media of at most the maximum breakpoint width. No query for the largest breakpoint.\n// Makes the @content apply to the given breakpoint and narrower.\n@mixin media-breakpoint-down($name, $breakpoints: $grid-breakpoints) {\n  $max: breakpoint-max($name, $breakpoints);\n  @if $max {\n    @media (max-width: $max) {\n      @content;\n    }\n  } @else {\n    @content;\n  }\n}\n\n// Media between the breakpoint's minimum and maximum widths.\n// No minimum for the smallest breakpoint, and no maximum for the largest one.\n// Makes the @content apply only to the given breakpoint, not viewports any wider or narrower.\n@mixin media-breakpoint-only($name, $breakpoints: $grid-breakpoints) {\n  @include media-breakpoint-up($name, $breakpoints) {\n    @include media-breakpoint-down($name, $breakpoints) {\n      @content;\n    }\n  }\n}\n\n// Media that spans multiple breakpoint widths.\n// Makes the @content apply between the min and max breakpoints\n@mixin media-breakpoint-between($lower, $upper, $breakpoints: $grid-breakpoints) {\n  @include media-breakpoint-up($lower, $breakpoints) {\n    @include media-breakpoint-down($upper, $breakpoints) {\n      @content;\n    }\n  }\n}\n","// Framework grid generation\n//\n// Used only by Bootstrap to generate the correct number of grid classes given\n// any value of `$grid-columns`.\n\n@mixin make-grid-columns($columns: $grid-columns, $gutter: $grid-gutter-width, $breakpoints: $grid-breakpoints) {\n  // Common properties for all breakpoints\n  %grid-column {\n    position: relative;\n    // Prevent columns from collapsing when empty\n    min-height: 1px;\n    // Inner gutter via padding\n    padding-left: ($gutter / 2);\n    padding-right: ($gutter / 2);\n  }\n  @each $breakpoint in map-keys($breakpoints) {\n    @for $i from 1 through $columns {\n      .col-#{$breakpoint}-#{$i} {\n        @extend %grid-column;\n      }\n    }\n    @include media-breakpoint-up($breakpoint) {\n      // Work around cross-media @extend (https://github.com/sass/sass/issues/1050)\n      %grid-column-float-#{$breakpoint} {\n        float: left;\n      }\n      @for $i from 1 through $columns {\n        .col-#{$breakpoint}-#{$i} {\n          @if not $enable-flex {\n            @extend %grid-column-float-#{$breakpoint};\n          }\n          @include make-col-span($i, $columns);\n        }\n      }\n      @each $modifier in (pull, push, offset) {\n        @for $i from 0 through $columns {\n          .col-#{$breakpoint}-#{$modifier}-#{$i} {\n            @include make-col-modifier($modifier, $i, $columns)\n          }\n        }\n      }\n    }\n  }\n}\n","//\n// Basic Bootstrap table\n//\n\n.table {\n  width: 100%;\n  max-width: 100%;\n  margin-bottom: $spacer;\n\n  th,\n  td {\n    padding: $table-cell-padding;\n    line-height: $line-height;\n    vertical-align: top;\n    border-top: $table-border-width solid $table-border-color;\n  }\n\n  thead th {\n    vertical-align: bottom;\n    border-bottom: (2 * $table-border-width) solid $table-border-color;\n  }\n\n  tbody + tbody {\n    border-top: (2 * $table-border-width) solid $table-border-color;\n  }\n\n  .table {\n    background-color: $body-bg;\n  }\n}\n\n\n//\n// Condensed table w/ half padding\n//\n\n.table-sm {\n  th,\n  td {\n    padding: $table-sm-cell-padding;\n  }\n}\n\n\n// Bordered version\n//\n// Add borders all around the table and between all the columns.\n\n.table-bordered {\n  border: $table-border-width solid $table-border-color;\n\n  th,\n  td {\n    border: $table-border-width solid $table-border-color;\n  }\n\n  thead {\n    th,\n    td {\n      border-bottom-width: (2 * $table-border-width);\n    }\n  }\n}\n\n\n// Zebra-striping\n//\n// Default zebra-stripe styles (alternating gray and transparent backgrounds)\n\n.table-striped {\n  tbody tr:nth-of-type(odd) {\n    background-color: $table-bg-accent;\n  }\n}\n\n\n// Hover effect\n//\n// Placed here since it has to come after the potential zebra striping\n\n.table-hover {\n  tbody tr {\n    @include hover {\n      background-color: $table-bg-hover;\n    }\n  }\n}\n\n\n// Table backgrounds\n//\n// Exact selectors below required to override `.table-striped` and prevent\n// inheritance to nested tables.\n\n// Generate the contextual variants\n@include table-row-variant(active, $table-bg-active);\n@include table-row-variant(success, $state-success-bg);\n@include table-row-variant(info, $state-info-bg);\n@include table-row-variant(warning, $state-warning-bg);\n@include table-row-variant(danger, $state-danger-bg);\n\n\n// Responsive tables\n//\n// Wrap your tables in `.table-responsive` and we'll make them mobile friendly\n// by enabling horizontal scrolling. Only applies <768px. Everything above that\n// will display normally.\n\n.table-responsive {\n  display: block;\n  width: 100%;\n  min-height: 0.01%; // Workaround for IE9 bug (see https://github.com/twbs/bootstrap/issues/14837)\n  overflow-x: auto;\n\n  // TODO: find out if we need this still.\n  //\n  // border: $table-border-width solid $table-border-color;\n  // -ms-overflow-style: -ms-autohiding-scrollbar; // See https://github.com/twbs/bootstrap/pull/10057\n}\n\n\n.thead-inverse {\n  th {\n    color: #fff;\n    background-color: $gray-dark;\n  }\n}\n.thead-default {\n  th {\n    color: $gray;\n    background-color: $gray-lighter;\n  }\n}\n\n.table-inverse {\n  color: $gray-lighter;\n  background-color: $gray-dark;\n\n  &.table-bordered {\n    border: 0;\n  }\n\n  th,\n  td,\n  thead th {\n    border-color: $gray;\n  }\n}\n\n\n.table-reflow {\n  thead {\n    float: left;\n  }\n\n  tbody {\n    display: block;\n    white-space: nowrap;\n  }\n\n  th,\n  td {\n    border-top: $table-border-width solid $table-border-color;\n    border-left: $table-border-width solid $table-border-color;\n\n    &:last-child {\n      border-right: $table-border-width solid $table-border-color;\n    }\n  }\n\n  thead,\n  tbody,\n  tfoot {\n    &:last-child {\n      tr:last-child {\n        th,\n        td {\n          border-bottom: $table-border-width solid $table-border-color;\n        }\n      }\n    }\n  }\n\n  tr {\n    float: left;\n\n    th,\n    td {\n      display: block !important;\n      border: $table-border-width solid $table-border-color;\n    }\n  }\n}\n","// Tables\n\n@mixin table-row-variant($state, $background) {\n  // Exact selectors below required to override `.table-striped` and prevent\n  // inheritance to nested tables.\n  .table-#{$state} {\n    &,\n    > th,\n    > td {\n      background-color: $background;\n    }\n  }\n\n  // Hover states for `.table-hover`\n  // Note: this is not available for cells or rows within `thead` or `tfoot`.\n  .table-hover {\n    $hover-background: darken($background, 5%);\n\n    .table-#{$state} {\n      @include hover {\n        background-color: $hover-background;\n\n        > td,\n        > th {\n          background-color: $hover-background;\n        }\n      }\n    }\n  }\n}\n","//\n// Textual form controls\n//\n\n.form-control {\n  display: block;\n  width: 100%;\n  // // Make inputs at least the height of their button counterpart (base line-height + padding + border)\n  // height: $input-height;\n  padding: $input-padding-y $input-padding-x;\n  font-size: $font-size-base;\n  line-height: $line-height;\n  color: $input-color;\n  background-color: $input-bg;\n  // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214.\n  background-image: none;\n  border: $input-btn-border-width solid $input-border-color;\n  // Note: This has no effect on <select>s in some browsers, due to the limited stylability of `<select>`s in CSS.\n  @include border-radius($input-border-radius);\n  @include box-shadow($input-box-shadow);\n  @include transition(border-color ease-in-out .15s, box-shadow ease-in-out .15s);\n\n  // Make inputs at least the height of their button counterpart (base line-height + padding + border).\n  // Only apply the height to textual inputs and some selects.\n  // &:not(textarea),\n  // &:not(select[size]),\n  // &:not(select[multiple]) {\n  //   height: $input-height;\n  // }\n\n  // Unstyle the caret on `<select>`s in IE10+.\n  &::-ms-expand {\n    background-color: transparent;\n    border: 0;\n  }\n\n  // Customize the `:focus` state to imitate native WebKit styles.\n  @include form-control-focus();\n\n  // Placeholder\n  &::placeholder {\n    color: $input-color-placeholder;\n    // Override Firefox's unusual default opacity; see https://github.com/twbs/bootstrap/pull/11526.\n    opacity: 1;\n  }\n\n  // Disabled and read-only inputs\n  //\n  // HTML5 says that controls under a fieldset > legend:first-child won't be\n  // disabled if the fieldset is disabled. Due to implementation difficulty, we\n  // don't honor that edge case; we style them as disabled anyway.\n  &:disabled,\n  &[readonly] {\n    background-color: $input-bg-disabled;\n    // iOS fix for unreadable disabled content; see https://github.com/twbs/bootstrap/issues/11655.\n    opacity: 1;\n  }\n\n  &:disabled {\n    cursor: $cursor-disabled;\n  }\n}\n\n\n// Make file inputs better match text inputs by forcing them to new lines.\n.form-control-file,\n.form-control-range {\n  display: block;\n}\n\n\n//\n// Labels\n//\n\n// For use with horizontal and inline forms, when you need the label text to\n// align with the form controls.\n.form-control-label {\n  padding: $input-padding-y $input-padding-x;\n  margin-bottom: 0; // Override the `<label>` default\n}\n\n\n// Todo: clear this up\n\n// Special styles for iOS temporal inputs\n//\n// In Mobile Safari, setting `display: block` on temporal inputs causes the\n// text within the input to become vertically misaligned. As a workaround, we\n// set a pixel line-height that matches the given height of the input, but only\n// for Safari. See https://bugs.webkit.org/show_bug.cgi?id=139848\n//\n// Note that as of 8.3, iOS doesn't support `datetime` or `week`.\n\n@media screen and (-webkit-min-device-pixel-ratio: 0) {\n  input[type=\"date\"],\n  input[type=\"time\"],\n  input[type=\"datetime-local\"],\n  input[type=\"month\"] {\n    &.form-control {\n      line-height: $input-height;\n    }\n\n    &.input-sm,\n    .input-group-sm &.form-control {\n      line-height: $input-height-sm;\n    }\n\n    &.input-lg,\n    .input-group-lg &.form-control {\n      line-height: $input-height-lg;\n    }\n  }\n}\n\n\n// Static form control text\n//\n// Apply class to an element to make any string of text align with labels in a\n// horizontal form layout.\n\n.form-control-static {\n  min-height: $input-height;\n  // Size it appropriately next to real form controls\n  padding-top: $input-padding-y;\n  padding-bottom: $input-padding-y;\n  // Remove default margin from `p`\n  margin-bottom: 0;\n\n  &.form-control-sm,\n  &.form-control-lg {\n    padding-right: 0;\n    padding-left: 0;\n  }\n}\n\n\n// Form control sizing\n//\n// Build on `.form-control` with modifier classes to decrease or increase the\n// height and font-size of form controls.\n//\n// The `.form-group-* form-control` variations are sadly duplicated to avoid the\n// issue documented in https://github.com/twbs/bootstrap/issues/15074.\n\n.form-control-sm {\n  // height: $input-height-sm;\n  padding: $input-padding-y-sm $input-padding-x-sm;\n  font-size: $font-size-sm;\n  line-height: $line-height-sm;\n  @include border-radius($input-border-radius-sm);\n}\n\n.form-control-lg {\n  // height: $input-height-lg;\n  padding: $input-padding-y-lg $input-padding-x-lg;\n  font-size: $font-size-lg;\n  line-height: $line-height-lg;\n  @include border-radius($input-border-radius-lg);\n}\n\n\n// Form groups\n//\n// Designed to help with the organization and spacing of vertical forms. For\n// horizontal forms, use the predefined grid classes.\n\n.form-group {\n  margin-bottom: $form-group-margin-bottom;\n}\n\n\n// Checkboxes and radios\n//\n// Indent the labels to position radios/checkboxes as hanging controls.\n\n.radio,\n.checkbox {\n  position: relative;\n  display: block;\n  // margin-top:    ($spacer * .75);\n  margin-bottom: ($spacer * .75);\n\n  label {\n    padding-left: 1.25rem;\n    margin-bottom: 0;\n    font-weight: normal;\n    cursor: pointer;\n\n    // When there's no labels, don't position the input.\n    input:only-child {\n      position: static;\n    }\n  }\n}\n.radio input[type=\"radio\"],\n.radio-inline input[type=\"radio\"],\n.checkbox input[type=\"checkbox\"],\n.checkbox-inline input[type=\"checkbox\"] {\n  position: absolute;\n  margin-top: .25rem;\n  // margin-top: 4px \\9;\n  margin-left: -1.25rem;\n}\n\n.radio + .radio,\n.checkbox + .checkbox {\n  // Move up sibling radios or checkboxes for tighter spacing\n  margin-top: -.25rem;\n}\n\n// Radios and checkboxes on same line\n.radio-inline,\n.checkbox-inline {\n  position: relative;\n  display: inline-block;\n  padding-left: 1.25rem;\n  margin-bottom: 0;\n  font-weight: normal;\n  vertical-align: middle;\n  cursor: pointer;\n}\n.radio-inline + .radio-inline,\n.checkbox-inline + .checkbox-inline {\n  margin-top: 0;\n  margin-left: .75rem;\n}\n\n// Apply same disabled cursor tweak as for inputs\n// Some special care is needed because <label>s don't inherit their parent's `cursor`.\n//\n// Note: Neither radios nor checkboxes can be readonly.\ninput[type=\"radio\"],\ninput[type=\"checkbox\"] {\n  &:disabled,\n  &.disabled {\n    cursor: $cursor-disabled;\n  }\n}\n// These classes are used directly on <label>s\n.radio-inline,\n.checkbox-inline {\n  &.disabled {\n    cursor: $cursor-disabled;\n  }\n}\n// These classes are used on elements with <label> descendants\n.radio,\n.checkbox {\n  &.disabled {\n    label {\n      cursor: $cursor-disabled;\n    }\n  }\n}\n\n\n// Form control feedback states\n//\n// Apply contextual and semantic states to individual form controls.\n\n.form-control-success,\n.form-control-warning,\n.form-control-danger {\n  padding-right: ($input-padding-x * 3);\n  background-repeat: no-repeat;\n  background-position: center right ($input-height * .25);\n  background-size: ($input-height * .65) ($input-height * .65);\n}\n\n// Form validation states\n.has-success {\n  @include form-control-validation($brand-success);\n\n  .form-control-success {\n    background-image: url($form-icon-success);\n  }\n}\n\n.has-warning {\n  @include form-control-validation($brand-warning);\n\n  .form-control-warning {\n    background-image: url($form-icon-warning);\n  }\n}\n\n.has-danger {\n  @include form-control-validation($brand-danger);\n\n  .form-control-danger {\n    background-image: url($form-icon-danger);\n  }\n}\n\n\n\n\n// .form-control-success {\n//   background-image: url(\"#{$form-icon-success}\");\n//   border-color: $brand-success;\n// }\n//\n// .form-control-warning {\n//   background-image: url(\"#{$form-icon-warning}\");\n//   border-color: $brand-warning;\n// }\n//\n// .form-control-error {\n//   background-image: url(\"#{$form-icon-danger}\");\n//   border-color: $brand-danger;\n// }\n\n\n// .has-feedback {\n//   // Enable absolute positioning\n//   position: relative;\n//\n//   // Ensure icons don't overlap text\n//   .form-control {\n//     padding-right: ($input-height * 1.25);\n//   }\n// }\n// // Feedback icon\n// .form-control-feedback {\n//   position: absolute;\n//   top: 0;\n//   right: 0;\n//   z-index: 2; // Ensure icon is above input groups\n//   display: block;\n//   width: $input-height;\n//   height: $input-height;\n//   line-height: $input-height;\n//   text-align: center;\n//   pointer-events: none;\n// }\n// .input-lg + .form-control-feedback,\n// .input-group-lg + .form-control-feedback {\n//   width: $input-height-lg;\n//   height: $input-height-lg;\n//   line-height: $input-height-lg;\n// }\n// .input-sm + .form-control-feedback,\n// .input-group-sm + .form-control-feedback {\n//   width: $input-height-sm;\n//   height: $input-height-sm;\n//   line-height: $input-height-sm;\n// }\n//\n// // Form validation states\n// .has-success {\n//   @include form-control-validation($state-success-text, $state-success-text, $state-success-bg);\n// }\n// .has-warning {\n//   @include form-control-validation($state-warning-text, $state-warning-text, $state-warning-bg);\n// }\n// .has-danger {\n//   @include form-control-validation($state-danger-text, $state-danger-text, $state-danger-bg);\n// }\n//\n// // Reposition feedback icon if input has visible label above\n// .has-feedback label {\n//\n//   ~ .form-control-feedback {\n//     // TODO: redo this since we nuked the `$line-height-computed`\n//     top: 0; // Height of the `label` and its margin\n//   }\n//\n//   &.sr-only ~ .form-control-feedback {\n//     top: 0;\n//   }\n// }\n\n\n// Inline forms\n//\n// Make forms appear inline(-block) by adding the `.form-inline` class. Inline\n// forms begin stacked on extra small (mobile) devices and then go inline when\n// viewports reach <768px.\n//\n// Requires wrapping inputs and labels with `.form-group` for proper display of\n// default HTML form controls and our custom form controls (e.g., input groups).\n\n.form-inline {\n\n  // Kick in the inline\n  @include media-breakpoint-up(sm) {\n    // Inline-block all the things for \"inline\"\n    .form-group {\n      display: inline-block;\n      margin-bottom: 0;\n      vertical-align: middle;\n    }\n\n    // Allow folks to *not* use `.form-group`\n    .form-control {\n      display: inline-block;\n      width: auto; // Prevent labels from stacking above inputs in `.form-group`\n      vertical-align: middle;\n    }\n\n    // Make static controls behave like regular ones\n    .form-control-static {\n      display: inline-block;\n    }\n\n    .input-group {\n      display: inline-table;\n      vertical-align: middle;\n\n      .input-group-addon,\n      .input-group-btn,\n      .form-control {\n        width: auto;\n      }\n    }\n\n    // Input groups need that 100% width though\n    .input-group > .form-control {\n      width: 100%;\n    }\n\n    .form-control-label {\n      margin-bottom: 0;\n      vertical-align: middle;\n    }\n\n    // Remove default margin on radios/checkboxes that were used for stacking, and\n    // then undo the floating of radios and checkboxes to match.\n    .radio,\n    .checkbox {\n      display: inline-block;\n      margin-top: 0;\n      margin-bottom: 0;\n      vertical-align: middle;\n\n      label {\n        padding-left: 0;\n      }\n    }\n    .radio input[type=\"radio\"],\n    .checkbox input[type=\"checkbox\"] {\n      position: relative;\n      margin-left: 0;\n    }\n\n    // Re-override the feedback icon.\n    .has-feedback .form-control-feedback {\n      top: 0;\n    }\n  }\n}\n","// Form validation states\n//\n// Used in _forms.scss to generate the form validation CSS for warnings, errors,\n// and successes.\n\n@mixin form-control-validation($color) {\n  // Color the label and help text\n  .text-help,\n  .form-control-label,\n  .radio,\n  .checkbox,\n  .radio-inline,\n  .checkbox-inline,\n  &.radio label,\n  &.checkbox label,\n  &.radio-inline label,\n  &.checkbox-inline label  {\n    color: $color;\n  }\n  // Set the border and box shadow on specific inputs to match\n  .form-control {\n    border-color: $color;\n    // @include box-shadow(inset 0 1px 1px rgba(0,0,0,.075)); // Redeclare so transitions work\n\n    &:focus {\n      // border-color: darken($border-color, 10%);\n      // $shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 6px lighten($border-color, 20%);\n      // @include box-shadow($shadow);\n    }\n  }\n\n  // Set validation states also for addons\n  .input-group-addon {\n    color: $color;\n    border-color: $color;\n    background-color: lighten($color, 40%);\n  }\n  // Optional feedback icon\n  .form-control-feedback {\n    color: $color;\n  }\n}\n\n// Form control focus state\n//\n// Generate a customized focus state and for any input with the specified color,\n// which defaults to the `@input-border-focus` variable.\n//\n// We highly encourage you to not customize the default value, but instead use\n// this to tweak colors on an as-needed basis. This aesthetic change is based on\n// WebKit's default styles, but applicable to a wider range of browsers. Its\n// usability and accessibility should be taken into account with any change.\n//\n// Example usage: change the default blue border and shadow to white for better\n// contrast against a dark gray background.\n@mixin form-control-focus() {\n  &:focus {\n    border-color: $input-border-focus;\n    outline: none;\n    $shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px $input-box-shadow-focus;\n    @include box-shadow($shadow);\n  }\n}\n\n// Form control sizing\n//\n// Relative text size, padding, and border-radii changes for form controls. For\n// horizontal sizing, wrap controls in the predefined grid classes. `<select>`\n// element gets special love because it's special, and that's a fact!\n\n@mixin input-size($parent, $input-height, $padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius) {\n  #{$parent} {\n    height: $input-height;\n    padding: $padding-vertical $padding-horizontal;\n    font-size: $font-size;\n    line-height: $line-height;\n    @include border-radius($border-radius);\n  }\n\n  select#{$parent} {\n    height: $input-height;\n    line-height: $input-height;\n  }\n\n  textarea#{$parent},\n  select[multiple]#{$parent} {\n    height: auto;\n  }\n}\n","//\n// Base styles\n//\n\n.btn {\n  display: inline-block;\n  font-weight: $btn-font-weight;\n  text-align: center;\n  white-space: nowrap;\n  vertical-align: middle;\n  cursor: pointer;\n  user-select: none;\n  border: $input-btn-border-width solid transparent;\n  @include button-size($btn-padding-y, $btn-padding-x, $font-size-base, $line-height, $btn-border-radius);\n  @include transition(all .2s ease-in-out);\n\n  &,\n  &:active,\n  &.active {\n    &:focus,\n    &.focus {\n      @include tab-focus();\n    }\n  }\n\n  @include hover-focus {\n    text-decoration: none;\n  }\n  &.focus {\n    text-decoration: none;\n  }\n\n  &:active,\n  &.active {\n    background-image: none;\n    outline: 0;\n    @include box-shadow(inset 0 3px 5px rgba(0,0,0,.125));\n  }\n\n  &.disabled,\n  &:disabled {\n    cursor: $cursor-disabled;\n    opacity: .65;\n    @include box-shadow(none);\n  }\n}\n\n// Future-proof disabling of clicks on `<a>` elements\na.btn.disabled,\nfieldset[disabled] a.btn {\n  pointer-events: none;\n}\n\n\n//\n// Alternate buttons\n//\n\n.btn-primary {\n  @include button-variant($btn-primary-color, $btn-primary-bg, $btn-primary-border);\n}\n.btn-secondary {\n  @include button-variant($btn-secondary-color, $btn-secondary-bg, $btn-secondary-border);\n}\n.btn-info {\n  @include button-variant($btn-info-color, $btn-info-bg, $btn-info-border);\n}\n.btn-success {\n  @include button-variant($btn-success-color, $btn-success-bg, $btn-success-border);\n}\n.btn-warning {\n  @include button-variant($btn-warning-color, $btn-warning-bg, $btn-warning-border);\n}\n.btn-danger {\n  @include button-variant($btn-danger-color, $btn-danger-bg, $btn-danger-border);\n}\n\n// Remove all backgrounds\n.btn-primary-outline {\n  @include button-outline-variant($btn-primary-bg);\n}\n.btn-secondary-outline {\n  @include button-outline-variant($btn-secondary-border);\n}\n.btn-info-outline {\n  @include button-outline-variant($btn-info-bg);\n}\n.btn-success-outline {\n  @include button-outline-variant($btn-success-bg);\n}\n.btn-warning-outline {\n  @include button-outline-variant($btn-warning-bg);\n}\n.btn-danger-outline {\n  @include button-outline-variant($btn-danger-bg);\n}\n\n\n//\n// Link buttons\n//\n\n// Make a button look and behave like a link\n.btn-link {\n  font-weight: normal;\n  color: $link-color;\n  border-radius: 0;\n\n  &,\n  &:active,\n  &.active,\n  &:disabled {\n    background-color: transparent;\n    @include box-shadow(none);\n  }\n  &,\n  &:focus,\n  &:active {\n    border-color: transparent;\n  }\n  @include hover {\n    border-color: transparent;\n  }\n  @include hover-focus {\n    color: $link-hover-color;\n    text-decoration: $link-hover-decoration;\n    background-color: transparent;\n  }\n  &:disabled {\n    @include hover-focus {\n      color: $btn-link-disabled-color;\n      text-decoration: none;\n    }\n  }\n}\n\n\n//\n// Button Sizes\n//\n\n.btn-lg {\n  // line-height: ensure even-numbered height of button next to large input\n  @include button-size($btn-padding-y-lg, $btn-padding-x-lg, $font-size-lg, $line-height-lg, $btn-border-radius-lg);\n}\n.btn-sm {\n  // line-height: ensure proper height of button next to small input\n  @include button-size($btn-padding-y-sm, $btn-padding-x-sm, $font-size-sm, $line-height-sm, $btn-border-radius-sm);\n}\n\n\n//\n// Block button\n//\n\n.btn-block {\n  display: block;\n  width: 100%;\n}\n\n// Vertically space out multiple block buttons\n.btn-block + .btn-block {\n  margin-top: 5px;\n}\n\n// Specificity overrides\ninput[type=\"submit\"],\ninput[type=\"reset\"],\ninput[type=\"button\"] {\n  &.btn-block {\n    width: 100%;\n  }\n}\n","// Button variants\n//\n// Easily pump out default styles, as well as :hover, :focus, :active,\n// and disabled options for all buttons\n\n@mixin button-variant($color, $background, $border) {\n  $active-background: darken($background, 10%);\n  $active-border: darken($border, 12%);\n\n  color: $color;\n  background-color: $background;\n  border-color: $border;\n  @include box-shadow(inset 0 1px 0 rgba(255,255,255,.15), 0 1px 1px rgba(0,0,0,.075));\n\n  @include hover {\n    color: $color;\n    background-color: $active-background;\n        border-color: $active-border;\n  }\n\n  &:focus,\n  &.focus {\n    color: $color;\n    background-color: $active-background;\n        border-color: $active-border;\n  }\n\n  &:active,\n  &.active,\n  .open > &.dropdown-toggle {\n    color: $color;\n    background-color: $active-background;\n        border-color: $active-border;\n    // Remove the gradient for the pressed/active state\n    background-image: none;\n    @include box-shadow(inset 0 3px 5px rgba(0,0,0,.125));\n\n    &:hover,\n    &:focus,\n    &.focus {\n      color: $color;\n      background-color: darken($background, 17%);\n          border-color: darken($border, 25%);\n    }\n  }\n\n  &.disabled,\n  &:disabled {\n    &:focus,\n    &.focus {\n      background-color: $background;\n          border-color: $border;\n    }\n    @include hover {\n      background-color: $background;\n          border-color: $border;\n    }\n  }\n}\n\n@mixin button-outline-variant($color) {\n  color: $color;\n  background-image: none;\n  background-color: transparent;\n  border-color: $color;\n\n  &:focus,\n  &.focus,\n  &:active,\n  &.active,\n  .open > &.dropdown-toggle {\n    color: #fff;\n    background-color: $color;\n        border-color: $color;\n  }\n  @include hover {\n    color: #fff;\n    background-color: $color;\n        border-color: $color;\n  }\n\n  &.disabled,\n  &:disabled {\n    &:focus,\n    &.focus {\n      border-color: lighten($color, 20%);\n    }\n    @include hover {\n      border-color: lighten($color, 20%);\n    }\n  }\n}\n\n// Button sizes\n@mixin button-size($padding-y, $padding-x, $font-size, $line-height, $border-radius) {\n  padding: $padding-y $padding-x;\n  font-size: $font-size;\n  line-height: $line-height;\n  @include border-radius($border-radius);\n}\n",".fade {\n  opacity: 0;\n  transition: opacity .15s linear;\n\n  &.in {\n    opacity: 1;\n  }\n}\n\n.collapse {\n  display: none;\n\n  &.in {\n    display: block;\n  }\n  // tr&.in    { display: table-row; }\n  // tbody&.in { display: table-row-group; }\n}\n\n.collapsing {\n  position: relative;\n  height: 0;\n  overflow: hidden;\n  transition-timing-function: ease;\n  transition-duration: .35s;\n  transition-property: height;\n}\n","// The dropdown wrapper (`<div>`)\n.dropup,\n.dropdown {\n  position: relative;\n}\n\n.dropdown-toggle {\n  // Generate the caret automatically\n  &::after {\n    display: inline-block;\n    width: 0;\n    height: 0;\n    margin-right: .25rem;\n    margin-left: .25rem;\n    vertical-align: middle;\n    content: \"\";\n    border-top: $caret-width solid;\n    border-right: $caret-width solid transparent;\n    border-left: $caret-width solid transparent;\n  }\n\n  // Prevent the focus on the dropdown toggle when closing dropdowns\n  &:focus {\n    outline: 0;\n  }\n}\n\n.dropup {\n  .dropdown-toggle {\n    &::after {\n      border-top: 0;\n      border-bottom: $caret-width solid;\n    }\n  }\n}\n\n// The dropdown menu\n.dropdown-menu {\n  position: absolute;\n  top: 100%;\n  left: 0;\n  z-index: $zindex-dropdown;\n  display: none; // none by default, but block on \"open\" of the menu\n  float: left;\n  min-width: 160px;\n  padding: 5px 0;\n  margin: 2px 0 0; // override default ul\n  font-size: $font-size-base;\n  color: $body-color;\n  text-align: left; // Ensures proper alignment if parent has it changed (e.g., modal footer)\n  list-style: none;\n  background-color: $dropdown-bg;\n  background-clip: padding-box;\n  border: $dropdown-border-width solid $dropdown-border-color;\n  @include border-radius($border-radius);\n  @include box-shadow(0 6px 12px rgba(0,0,0,.175));\n}\n\n// Dividers (basically an `<hr>`) within the dropdown\n.dropdown-divider {\n  @include nav-divider($dropdown-divider-bg);\n}\n\n// Links, buttons, and more within the dropdown menu\n//\n// `<button>`-specific styles are denoted with `// For <button>s`\n.dropdown-item {\n  display: block;\n  width: 100%; // For `<button>`s\n  padding: 3px 20px;\n  clear: both;\n  font-weight: normal;\n  line-height: $line-height;\n  color: $dropdown-link-color;\n  text-align: inherit; // For `<button>`s\n  white-space: nowrap; // prevent links from randomly breaking onto new lines\n  background: none; // For `<button>`s\n  border: 0; // For `<button>`s\n\n  @include hover-focus {\n    color: $dropdown-link-hover-color;\n    text-decoration: none;\n    background-color: $dropdown-link-hover-bg;\n  }\n\n  // Active state\n  &.active {\n    @include plain-hover-focus {\n      color: $dropdown-link-active-color;\n      text-decoration: none;\n      background-color: $dropdown-link-active-bg;\n      outline: 0;\n    }\n  }\n\n  // Disabled state\n  //\n  // Gray out text and ensure the hover/focus state remains gray\n  &.disabled {\n    @include plain-hover-focus {\n      color: $dropdown-link-disabled-color;\n    }\n\n    // Nuke hover/focus effects\n    @include hover-focus {\n      text-decoration: none;\n      cursor: $cursor-disabled;\n      background-color: transparent;\n      background-image: none; // Remove CSS gradient\n      @include reset-filter();\n    }\n  }\n}\n\n// Open state for the dropdown\n.open {\n  // Show the menu\n  > .dropdown-menu {\n    display: block;\n  }\n\n  // Remove the outline when :focus is triggered\n  > a {\n    outline: 0;\n  }\n}\n\n// Menu positioning\n//\n// Add extra class to `.dropdown-menu` to flip the alignment of the dropdown\n// menu with the parent.\n.dropdown-menu-right {\n  right: 0;\n  left: auto; // Reset the default from `.dropdown-menu`\n}\n// With v3, we enabled auto-flipping if you have a dropdown within a right\n// aligned nav component. To enable the undoing of that, we provide an override\n// to restore the default dropdown menu alignment.\n//\n// This is only for left-aligning a dropdown menu within a `.navbar-right` or\n// `.pull-right` nav component.\n.dropdown-menu-left {\n  right: auto;\n  left: 0;\n}\n\n// Dropdown section headers\n.dropdown-header {\n  display: block;\n  padding: 3px 20px;\n  font-size: $font-size-sm;\n  line-height: $line-height;\n  color: $dropdown-header-color;\n  white-space: nowrap; // as with > li > a\n}\n\n// Backdrop to catch body clicks on mobile, etc.\n.dropdown-backdrop {\n  position: fixed;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  z-index: ($zindex-dropdown - 10);\n}\n\n// Right aligned dropdowns\n.pull-right > .dropdown-menu {\n  right: 0;\n  left: auto;\n}\n\n// Allow for dropdowns to go bottom up (aka, dropup-menu)\n//\n// Just add .dropup after the standard .dropdown class and you're set.\n// TODO: abstract this so that the navbar fixed styles are not placed here?\n\n.dropup,\n.navbar-fixed-bottom .dropdown {\n  // Reverse the caret\n  .caret {\n    content: \"\";\n    border-top: 0;\n    border-bottom: $caret-width solid;\n  }\n\n  // Different positioning for bottom up menu\n  .dropdown-menu {\n    top: auto;\n    bottom: 100%;\n    margin-bottom: 2px;\n  }\n}\n","// Horizontal dividers\n//\n// Dividers (basically an hr) within dropdowns a