e: none;\n    @include box-shadow(inset 0 1px 2px rgba(0, 0, 0, 0.075), 0 0 5px rgba(81, 167, 232, 0.5));\n  }\n\n  // Hides the default caret in IE11\n  &::-ms-expand {\n    opacity: 0;\n  }\n}\n\n.c-select-sm {\n  padding-top: 3px;\n  padding-bottom: 3px;\n  font-size: 12px;\n\n  &:not([multiple]) {\n    height: 26px;\n    min-height: 26px;\n  }\n}\n\n\n// File\n//\n// Custom file input.\n\n.file {\n  position: relative;\n  display: inline-block;\n  height: 2.5rem;\n  cursor: pointer;\n}\n.file input {\n  min-width: 14rem;\n  margin: 0;\n  filter: alpha(opacity = 0);\n  opacity: 0;\n}\n.file-custom {\n  position: absolute;\n  top: 0;\n  right: 0;\n  left: 0;\n  z-index: 5;\n  height: 2.5rem;\n  padding: .5rem 1rem;\n  line-height: 1.5;\n  color: #555;\n  user-select: none;\n  background-color: #fff;\n  border: $input-btn-border-width solid #ddd;\n  border-radius: .25rem;\n  @include box-shadow(inset 0 .2rem .4rem rgba(0,0,0,.05));\n}\n.file-custom::after {\n  content: \"Choose file...\";\n}\n.file-custom::before {\n  position: absolute;\n  top: -.075rem;\n  right: -.075rem;\n  bottom: -.075rem;\n  z-index: 6;\n  display: block;\n  height: 2.5rem;\n  padding: .5rem 1rem;\n  line-height: 1.5;\n  color: #555;\n  content: \"Browse\";\n  background-color: #eee;\n  border: $input-btn-border-width solid #ddd;\n  border-radius: 0 .25rem .25rem 0;\n}\n\n// Focus state\n.file input:focus ~ .file-custom {\n  @include box-shadow(0 0 0 .075rem #fff, 0 0 0 .2rem #0074d9);\n}\n","// Base class\n//\n// Kickstart any navigation component with a set of style resets. Works with\n// `<nav>`s or `<ul>`s.\n\n.nav {\n  padding-left: 0;\n  margin-bottom: 0;\n  list-style: none;\n}\n\n.nav-link {\n  display: inline-block;\n\n  @include hover-focus {\n    text-decoration: none;\n  }\n\n  // Disabled state lightens text and removes hover/tab effects\n  &.disabled {\n    color: $nav-disabled-link-color;\n\n    @include plain-hover-focus {\n      color: $nav-disabled-link-hover-color;\n      cursor: $cursor-disabled;\n      background-color: transparent;\n    }\n  }\n}\n\n\n// Nav inline\n\n.nav-inline {\n  .nav-item {\n    display: inline-block;\n  }\n\n  .nav-item + .nav-item,\n  .nav-link + .nav-link {\n    margin-left: 1rem;\n  }\n}\n\n\n//\n// Tabs\n//\n\n.nav-tabs {\n  border-bottom: 1px solid $nav-tabs-border-color;\n  @include clearfix();\n\n  .nav-item {\n    float: left;\n    // Make the list-items overlay the bottom border\n    margin-bottom: -1px;\n\n    + .nav-item {\n      margin-left: .2rem;\n    }\n  }\n\n  .nav-link {\n    display: block;\n    padding: $nav-link-padding;\n    border: $nav-tabs-link-border-width solid transparent;\n    @include border-radius($border-radius $border-radius 0 0);\n\n    @include hover-focus {\n      border-color: $nav-tabs-link-hover-border-color $nav-tabs-link-hover-border-color $nav-tabs-border-color;\n    }\n\n    &.disabled {\n      @include plain-hover-focus {\n        color: $nav-disabled-link-color;\n        background-color: transparent;\n        border-color: transparent;\n      }\n    }\n  }\n\n  .nav-link.active,\n  .nav-item.open .nav-link {\n    @include plain-hover-focus {\n      color: $nav-tabs-active-link-hover-color;\n      background-color: $nav-tabs-active-link-hover-bg;\n      border-color: $nav-tabs-active-link-hover-border-color $nav-tabs-active-link-hover-border-color transparent;\n    }\n  }\n}\n\n\n//\n// Pills\n//\n\n.nav-pills {\n  @include clearfix();\n\n  .nav-item {\n    float: left;\n\n    + .nav-item {\n      margin-left: .2rem;\n    }\n  }\n\n  .nav-link {\n    display: block;\n    padding: $nav-link-padding;\n    @include border-radius($nav-pills-border-radius);\n  }\n\n  .nav-link.active,\n  .nav-item.open .nav-link {\n    @include plain-hover-focus {\n      color: $component-active-color;\n      cursor: default;\n      background-color: $component-active-bg;\n    }\n  }\n}\n\n.nav-stacked {\n  .nav-item {\n    display: block;\n    float: none;\n\n    + .nav-item {\n      margin-top: .2rem;\n      margin-left: 0;\n    }\n  }\n}\n\n\n//\n// Tabbable tabs\n//\n\n// Hide tabbable panes to start, show them when `.active`\n.tab-content {\n  > .tab-pane {\n    display: none;\n  }\n  > .active {\n    display: block;\n  }\n}\n\n\n//\n// Dropdowns\n//\n\n.nav-tabs .dropdown-menu {\n  // Make dropdown border overlap tab border\n  margin-top: -1px;\n  // Remove the top rounded corners here since there is a hard edge above the menu\n  @include border-top-radius(0);\n}\n","// Wrapper and base class\n//\n// Provide a static navbar from which we expand to create full-width, fixed, and\n// other navbar variations.\n\n.navbar {\n  position: relative;\n  padding: $navbar-padding-vertical $navbar-padding-horizontal;\n  @include clearfix;\n\n  @include media-breakpoint-up(sm) {\n    @include border-radius($navbar-border-radius);\n  }\n}\n\n\n// Navbar alignment options\n//\n// Display the navbar across the entirety of the page or fixed it to the top or\n// bottom of the page.\n\n// A static, full width modifier with no rounded corners.\n.navbar-full {\n  z-index: $zindex-navbar;\n\n  @include media-breakpoint-up(sm) {\n    @include border-radius(0);\n  }\n}\n\n// Fix the top/bottom navbars when screen real estate supports it\n.navbar-fixed-top,\n.navbar-fixed-bottom {\n  position: fixed;\n  right: 0;\n  left: 0;\n  z-index: $zindex-navbar-fixed;\n\n  // Undo the rounded corners\n  @include media-breakpoint-up(sm) {\n    @include border-radius(0);\n  }\n}\n\n.navbar-fixed-top {\n  top: 0;\n}\n\n.navbar-fixed-bottom {\n  bottom: 0;\n}\n\n.navbar-sticky-top {\n  position: sticky;\n  top: 0;\n  z-index: $zindex-navbar-sticky;\n  width: 100%;\n\n  // Undo the rounded corners\n  @include media-breakpoint-up(sm) {\n    @include border-radius(0);\n  }\n}\n\n\n//\n// Brand/project name\n//\n\n.navbar-brand {\n  float: left;\n  padding-top:    .25rem;\n  padding-bottom: .25rem;\n  margin-right: 1rem;\n  font-size: $font-size-lg;\n\n  @include hover-focus {\n    text-decoration: none;\n  }\n\n  > img {\n    display: block;\n  }\n}\n\n\n.navbar-divider {\n  float: left;\n  width: 1px;\n  padding-top: .425rem;\n  padding-bottom: .425rem;\n  margin-right: $navbar-padding-horizontal;\n  margin-left:  $navbar-padding-horizontal;\n  overflow: hidden;\n\n  &::before {\n    content: \"\\00a0\";\n  }\n}\n\n\n// Navbar toggle\n//\n// Custom button for toggling the `.navbar-collapse`, powered by the collapse\n// Bootstrap JavaScript plugin.\n\n.navbar-toggler {\n  padding: .5rem .75rem;\n  font-size: $font-size-lg;\n  line-height: 1;\n  background: none;\n  border: $border-width solid transparent;\n  @include border-radius($btn-border-radius);\n\n  @include hover-focus {\n    text-decoration: none;\n  }\n}\n\n// Custom override for\n.navbar-toggleable {\n  &-xs {\n    @include media-breakpoint-up(sm) {\n      display: block !important;\n    }\n  }\n  &-sm {\n    @include media-breakpoint-up(md) {\n      display: block !important;\n    }\n  }\n  &-md {\n    @include media-breakpoint-up(lg) {\n      display: block !important;\n    }\n  }\n}\n\n\n// Navigation\n//\n// Custom navbar navigation built on the base `.nav` styles.\n\n.navbar-nav {\n  .nav-item {\n    float: left;\n  }\n\n  .nav-link {\n    display: block;\n    padding-top:    .425rem;\n    padding-bottom: .425rem;\n\n    + .nav-link {\n      margin-left: 1rem;\n    }\n  }\n\n  .nav-item + .nav-item {\n    margin-left: 1rem;\n  }\n}\n\n// Dark links against a light background\n.navbar-light {\n  .navbar-brand {\n    color: $navbar-light-active-color;\n\n    @include hover-focus {\n      color: $navbar-light-active-color;\n    }\n  }\n\n  .navbar-nav {\n    .nav-link {\n      color: $navbar-light-color;\n\n      @include hover-focus {\n        color: $navbar-light-hover-color;\n      }\n    }\n\n    .open > .nav-link,\n    .active > .nav-link,\n    .nav-link.open,\n    .nav-link.active {\n      @include plain-hover-focus {\n        color: $navbar-light-active-color;\n      }\n    }\n  }\n\n  .navbar-divider {\n    background-color: rgba(0,0,0,.075);\n  }\n}\n\n// White links against a dark background\n.navbar-dark {\n  .navbar-brand {\n    color: $navbar-dark-active-color;\n\n    @include hover-focus {\n      color: $navbar-dark-active-color;\n    }\n  }\n\n  .navbar-nav {\n    .nav-link {\n      color: $navbar-dark-color;\n\n      @include hover-focus {\n        color: $navbar-dark-hover-color;\n      }\n    }\n\n    .open > .nav-link,\n    .active > .nav-link,\n    .nav-link.open,\n    .nav-link.active {\n      @include plain-hover-focus {\n        color: $navbar-dark-active-color;\n      }\n    }\n  }\n\n  .navbar-divider {\n    background-color: rgba(255,255,255,.075);\n  }\n}\n","//\n// Base styles\n//\n\n.card {\n  position: relative;\n  display: block;\n  margin-bottom: $card-spacer-y;\n  background-color: $card-bg;\n  border: $card-border-width solid $card-border-color;\n  @include border-radius($card-border-radius);\n}\n\n.card-block {\n  padding: $card-spacer-x;\n}\n\n.card-title {\n  margin-bottom: $card-spacer-y;\n}\n\n.card-subtitle {\n  margin-top: -($card-spacer-y / 2);\n  margin-bottom: 0;\n}\n\n.card-text:last-child {\n  margin-bottom: 0;\n}\n\n// .card-actions {\n//   padding: $card-spacer-y $card-spacer-x;\n\n//   .card-link + .card-link {\n//     margin-left: $card-spacer-x;\n//   }\n// }\n\n.card-link {\n  @include hover {\n    text-decoration: none;\n  }\n\n  + .card-link {\n    margin-left: $card-spacer-x;\n  }\n}\n\n@if $enable-rounded {\n  .card {\n    > .list-group:first-child {\n      .list-group-item:first-child {\n        border-radius: $card-border-radius $card-border-radius 0 0;\n      }\n    }\n\n    > .list-group:last-child {\n      .list-group-item:last-child {\n        border-radius: 0 0 $card-border-radius $card-border-radius;\n      }\n    }\n  }\n}\n\n\n//\n// Optional textual caps\n//\n\n.card-header {\n  padding: $card-spacer-y $card-spacer-x;\n  background-color: $card-cap-bg;\n  border-bottom: $card-border-width solid $card-border-color;\n\n  &:first-child {\n    @include border-radius($card-border-radius-inner $card-border-radius-inner 0 0);\n  }\n}\n\n.card-footer {\n  padding: $card-spacer-y $card-spacer-x;\n  background-color: $card-cap-bg;\n  border-top: $card-border-width solid $card-border-color;\n\n  &:last-child {\n    @include border-radius(0 0 $card-border-radius-inner $card-border-radius-inner);\n  }\n}\n\n\n//\n// Background variations\n//\n\n.card-primary {\n  @include card-variant($brand-primary, $brand-primary);\n}\n.card-success {\n  @include card-variant($brand-success, $brand-success);\n}\n.card-info {\n  @include card-variant($brand-info, $brand-info);\n}\n.card-warning {\n  @include card-variant($brand-warning, $brand-warning);\n}\n.card-danger {\n  @include card-variant($brand-danger, $brand-danger);\n}\n\n// Remove all backgrounds\n.card-primary-outline {\n  @include card-outline-variant($btn-primary-bg);\n}\n.card-secondary-outline {\n  @include card-outline-variant($btn-secondary-border);\n}\n.card-info-outline {\n  @include card-outline-variant($btn-info-bg);\n}\n.card-success-outline {\n  @include card-outline-variant($btn-success-bg);\n}\n.card-warning-outline {\n  @include card-outline-variant($btn-warning-bg);\n}\n.card-danger-outline {\n  @include card-outline-variant($btn-danger-bg);\n}\n\n//\n// Inverse text within a card for use with dark backgrounds\n//\n\n.card-inverse {\n  @include card-inverse;\n}\n\n//\n// Blockquote\n//\n\n.card-blockquote {\n  padding: 0;\n  margin-bottom: 0;\n  border-left: 0;\n}\n\n// Card image\n.card-img {\n  // margin: -1.325rem;\n  @include border-radius(.25rem);\n}\n.card-img-overlay {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  padding: 1.25rem;\n}\n\n\n\n// Card image caps\n.card-img-top {\n  @include border-radius($card-border-radius-inner $card-border-radius-inner 0 0);\n}\n.card-img-bottom {\n  @include border-radius(0 0 $card-border-radius-inner $card-border-radius-inner);\n}\n\n\n//\n// Card set\n//\n\n@if $enable-flex {\n  @include media-breakpoint-up(sm) {\n    .card-deck {\n      display: flex;\n      flex-flow: row wrap;\n      margin-right: -.625rem;\n      margin-left: -.625rem;\n\n      .card {\n        flex: 1 0 0;\n        margin-right: .625rem;\n        margin-left: .625rem;\n      }\n    }\n  }\n} @else {\n  @include media-breakpoint-up(sm) {\n    .card-deck {\n      display: table;\n      table-layout: fixed;\n      border-spacing: 1.25rem 0;\n\n      .card {\n        display: table-cell;\n        width: 1%;\n        vertical-align: top;\n      }\n    }\n    .card-deck-wrapper {\n      margin-right: -1.25rem;\n      margin-left: -1.25rem;\n    }\n  }\n}\n\n//\n// Card groups\n//\n\n@include media-breakpoint-up(sm) {\n  .card-group {\n    @if $enable-flex {\n      display: flex;\n      flex-flow: row wrap;\n    } @else {\n      display: table;\n      width: 100%;\n      table-layout: fixed;\n    }\n\n    .card {\n      @if $enable-flex {\n        flex: 1 0 0;\n      } @else {\n        display: table-cell;\n        vertical-align: top;\n      }\n\n      + .card {\n        margin-left: 0;\n        border-left: 0;\n      }\n\n      // Handle rounded corners\n      @if $enable-rounded {\n        &:first-child {\n          @include border-right-radius(0);\n\n          .card-img-top {\n            border-top-right-radius: 0;\n          }\n          .card-img-bottom {\n            border-bottom-right-radius: 0;\n          }\n        }\n        &:last-child {\n          @include border-left-radius(0);\n\n          .card-img-top {\n            border-top-left-radius: 0;\n          }\n          .card-img-bottom {\n            border-bottom-left-radius: 0;\n          }\n        }\n\n        &:not(:first-child):not(:last-child) {\n          border-radius: 0;\n\n          .card-img-top,\n          .card-img-bottom {\n            border-radius: 0;\n          }\n        }\n      }\n    }\n  }\n}\n\n\n//\n// Card\n//\n\n@include media-breakpoint-up(sm) {\n  .card-columns {\n    column-count: 3;\n    column-gap: 1.25rem;\n\n    .card {\n      display: inline-block;\n      width: 100%; // Don't let them exceed the column width\n    }\n  }\n}\n","// Card variants\n\n@mixin card-variant($background, $border) {\n  background-color: $background;\n  border-color: $border;\n}\n\n@mixin card-outline-variant($color) {\n  background-color: transparent;\n  border-color: $color;\n}\n\n//\n// Inverse text within a card for use with dark backgrounds\n//\n\n@mixin card-inverse {\n  .card-header,\n  .card-footer {\n    border-bottom: $card-border-width solid rgba(255,255,255,.2);\n  }\n  .card-header,\n  .card-footer,\n  .card-title,\n  .card-blockquote {\n    color: #fff;\n  }\n  .card-link,\n  .card-text,\n  .card-blockquote > footer {\n    color: rgba(255,255,255,.65);\n  }\n  .card-link {\n    @include hover-focus {\n      color: $card-link-hover-color;\n    }\n  }\n}\n",".breadcrumb {\n  padding: $breadcrumb-padding-vertical $breadcrumb-padding-horizontal;\n  margin-bottom: $spacer-y;\n  list-style: none;\n  background-color: $breadcrumb-bg;\n  @include border-radius($border-radius);\n  @include clearfix;\n\n  > li {\n    float: left;\n\n    + li::before {\n      padding-right: .5rem;\n      padding-left: .5rem;\n      color: $breadcrumb-divider-color;\n      content: \"#{$breadcrumb-divider}\";\n    }\n  }\n\n  > .active {\n    color: $breadcrumb-active-color;\n  }\n}\n",".pagination {\n  display: inline-block;\n  padding-left: 0;\n  margin-top: $spacer-y;\n  margin-bottom: $spacer-y;\n  @include border-radius();\n}\n\n.page-item {\n  display: inline; // Remove list-style and block-level defaults\n\n  &:first-child {\n    .page-link {\n      margin-left: 0;\n      @include border-left-radius($border-radius);\n    }\n  }\n  &:last-child {\n    .page-link {\n      @include border-right-radius($border-radius);\n    }\n  }\n\n  &.active .page-link {\n    @include plain-hover-focus {\n      z-index: 2;\n      color: $pagination-active-color;\n      cursor: default;\n      background-color: $pagination-active-bg;\n      border-color: $pagination-active-border;\n    }\n  }\n\n  &.disabled .page-link {\n    @include plain-hover-focus {\n      color: $pagination-disabled-color;\n      cursor: $cursor-disabled;\n      background-color: $pagination-disabled-bg;\n      border-color: $pagination-disabled-border;\n    }\n  }\n}\n\n.page-link {\n  position: relative;\n  float: left; // Collapse white-space\n  padding: $pagination-padding-y $pagination-padding-x;\n  margin-left: -1px;\n  line-height: $line-height;\n  color: $pagination-color;\n  text-decoration: none;\n  background-color: $pagination-bg;\n  border: $pagination-border-width solid $pagination-border-color;\n\n  @include hover-focus {\n    color: $pagination-hover-color;\n    background-color: $pagination-hover-bg;\n    border-color: $pagination-hover-border;\n  }\n}\n\n\n//\n// Sizing\n//\n\n.pagination-lg {\n  @include pagination-size($pagination-padding-y-lg, $pagination-padding-x-lg, $font-size-lg, $line-height-lg, $border-radius-lg);\n}\n\n.pagination-sm {\n  @include pagination-size($pagination-padding-y-sm, $pagination-padding-x-sm, $font-size-sm, $line-height-sm, $border-radius-sm);\n}\n","// Pagination\n\n@mixin pagination-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius) {\n  .page-link {\n    padding: $padding-vertical $padding-horizontal;\n    font-size: $font-size;\n    line-height: $line-height;\n  }\n\n  .page-item {\n    &:first-child {\n      .page-link {\n        @include border-left-radius($border-radius);\n      }\n    }\n    &:last-child {\n      .page-link {\n        @include border-right-radius($border-radius);\n      }\n    }\n  }\n}\n",".pager {\n  padding-left: 0;\n  margin-top: $spacer-y;\n  margin-bottom: $spacer-y;\n  text-align: center;\n  list-style: none;\n  @include clearfix();\n\n  li {\n    display: inline;\n\n    > a,\n    > span {\n      display: inline-block;\n      padding: 5px 14px;\n      background-color: $pager-bg;\n      border: $pager-border-width solid $pager-border-color;\n      border-radius: $pager-border-radius;\n    }\n\n    > a {\n      @include hover-focus {\n        text-decoration: none;\n        background-color: $pager-hover-bg;\n      }\n    }\n  }\n\n  .disabled {\n    > a {\n      @include plain-hover-focus {\n        color: $pager-disabled-color;\n        cursor: $cursor-disabled;\n        background-color: $pager-bg;\n      }\n    }\n    > span {\n      color: $pager-disabled-color;\n      cursor: $cursor-disabled;\n      background-color: $pager-bg;\n    }\n  }\n}\n\n.pager-next {\n  > a,\n  > span {\n    float: right;\n  }\n}\n\n.pager-prev {\n  > a,\n  > span {\n    float: left;\n  }\n}\n","// Base class\n//\n// Requires one of the contextual, color modifier classes for `color` and\n// `background-color`.\n\n.label {\n  display: inline-block;\n  padding: .25em .4em;\n  font-size: 75%;\n  font-weight: $label-font-weight;\n  line-height: 1;\n  color: $label-color;\n  text-align: center;\n  white-space: nowrap;\n  vertical-align: baseline;\n  @include border-radius();\n\n  // Empty labels collapse automatically\n  &:empty {\n    display: none;\n  }\n}\n\n// Quick fix for labels in buttons\n.btn .label {\n  position: relative;\n  top: -1px;\n}\n\n// Add hover effects, but only for links\na.label {\n  @include hover-focus {\n    color: $label-link-hover-color;\n    text-decoration: none;\n    cursor: pointer;\n  }\n}\n\n// Pill labels\n//\n// Make them extra rounded with a modifier to replace v3's badges.\n\n.label-pill {\n  padding-right: .6em;\n  padding-left: .6em;\n  // Use a higher than normal value to ensure completely rounded edges when\n  // customizing padding or font-size on labels.\n  @include border-radius(10rem);\n}\n\n// Colors\n//\n// Contextual variations (linked labels get darker on :hover).\n\n.label-default {\n  @include label-variant($label-default-bg);\n}\n\n.label-primary {\n  @include label-variant($label-primary-bg);\n}\n\n.label-success {\n  @include label-variant($label-success-bg);\n}\n\n.label-info {\n  @include label-variant($label-info-bg);\n}\n\n.label-warning {\n  @include label-variant($label-warning-bg);\n}\n\n.label-danger {\n  @include label-variant($label-danger-bg);\n}\n","// Labels\n\n@mixin label-variant($color) {\n  background-color: $color;\n\n  &[href] {\n    @include hover-focus {\n      background-color: darken($color, 10%);\n    }\n  }\n}\n",".jumbotron {\n  padding: $jumbotron-padding ($jumbotron-padding / 2);\n  margin-bottom: $jumbotron-padding;\n  background-color: $jumbotron-bg;\n  @include border-radius($border-radius-lg);\n\n  @include media-breakpoint-up(sm) {\n    padding: ($jumbotron-padding * 2) $jumbotron-padding;\n  }\n}\n\n.jumbotron-hr {\n  border-top-color: darken($jumbotron-bg, 10%);\n}\n\n.jumbotron-fluid {\n  padding-right: 0;\n  padding-left: 0;\n  @include border-radius(0);\n}\n","//\n// Base styles\n//\n\n.alert {\n  padding: $alert-padding;\n  margin-bottom: $spacer-y;\n  border: $alert-border-width solid transparent;\n  @include border-radius($alert-border-radius);\n\n  // Improve alignment and spacing of inner content\n  > p,\n  > ul {\n    margin-bottom: 0;\n  }\n  > p + p {\n    margin-top: 5px;\n  }\n}\n\n// Headings for larger alerts\n.alert-heading {\n  // Specified to prevent conflicts of changing $headings-color\n  color: inherit;\n}\n\n// Provide class for links that match alerts\n.alert-link {\n  font-weight: $alert-link-font-weight;\n}\n\n\n// Dismissible alerts\n//\n// Expand the right padding and account for the close button's positioning.\n\n.alert-dismissible {\n  padding-right: ($alert-padding + 20);\n\n  // Adjust close link position\n  .close {\n    position: relative;\n    top: -2px;\n    right: -21px;\n    color: inherit;\n  }\n}\n\n\n// Alternate styles\n//\n// Generate contextual modifier classes for colorizing the alert.\n\n.alert-success {\n  @include alert-variant($alert-success-bg, $alert-success-border, $alert-success-text);\n}\n.alert-info {\n  @include alert-variant($alert-info-bg, $alert-info-border, $alert-info-text);\n}\n.alert-warning {\n  @include alert-variant($alert-warning-bg, $alert-warning-border, $alert-warning-text);\n}\n.alert-danger {\n  @include alert-variant($alert-danger-bg, $alert-danger-border, $alert-danger-text);\n}\n","// Alerts\n\n@mixin alert-variant($background, $border, $body-color) {\n  background-color: $background;\n  border-color: $border;\n  color: $body-color;\n\n  hr {\n    border-top-color: darken($border, 5%);\n  }\n  .alert-link {\n    color: darken($body-color, 10%);\n  }\n}\n","//\n// Progress animations\n//\n\n@keyframes progress-bar-stripes {\n  from  { background-position: $spacer-y 0; }\n  to    { background-position: 0 0; }\n}\n\n\n//\n// Basic progress bar\n//\n\n.progress {\n  display: block;\n  width: 100%;\n  height: $spacer-y; // todo: make a new var for this\n  margin-bottom: $spacer-y;\n}\n.progress[value] {\n  // IE10 uses `color` to set the bar background-color\n  color: #0074d9;\n  // Remove Firefox and Opera border\n  border: 0;\n  // Reset the default appearance\n  appearance: none;\n}\n.progress[value]::-webkit-progress-bar {\n  background-color: #eee;\n  @include border-radius($border-radius);\n  @include box-shadow(inset 0 .1rem .1rem rgba(0,0,0,.1));\n}\n.progress[value]::-webkit-progress-value::before {\n  content: attr(value);\n}\n.progress[value]::-webkit-progress-value {\n  background-color: #0074d9;\n  border-top-left-radius: $border-radius;\n  border-bottom-left-radius: $border-radius;\n}\n.progress[value=\"100\"]::-webkit-progress-value {\n  border-top-right-radius: $border-radius;\n  border-bottom-right-radius: $border-radius;\n}\n\n// Firefox styles must be entirely separate or it busts Webkit styles.\n//\n// Commented out for now because linter.\n//\n// $-moz-document url-prefix() {\n//   .progress[value] {\n//     background-color: #eee;\n//     .border-radius($border-radius);\n//     .box-shadow(inset 0 .1rem .1rem rgba(0,0,0,.1));\n//   }\n//   .progress[value]::-moz-progress-bar {\n//     background-color: #0074d9;\n//     border-top-left-radius: $border-radius;\n//     border-bottom-left-radius: $border-radius;\n//   }\n//   .progress[value=\"0\"]::-moz-progress-bar {\n//     color: $gray-light;\n//     min-width: 2rem;\n//     background-color: transparent;\n//     background-image: none;\n//   }\n//   .progress[value=\"100\"]::-moz-progress-bar {\n//     border-top-right-radius: $border-radius;\n//     border-bottom-right-radius: $border-radius;\n//   }\n// }\n\n// IE9 hacks to accompany custom markup. We don't need to scope this via media queries, but I feel better doing it anyway.\n@media screen and (min-width:0\\0) {\n  .progress {\n    background-color: #eee;\n    @include border-radius($border-radius);\n    @include box-shadow(inset 0 .1rem .1rem rgba(0,0,0,.1));\n  }\n  .progress-bar {\n    display: inline-block;\n    height: $spacer-y;\n    text-indent: -999rem; // Simulate hiding of value as in native `<progress>`\n    background-color: #0074d9;\n    border-top-left-radius: $border-radius;\n    border-bottom-left-radius: $border-radius;\n  }\n  .progress[width^=\"0\"] {\n    min-width: 2rem;\n    color: $gray-light;\n    background-color: transparent;\n    background-image: none;\n  }\n  .progress[width=\"100%\"] {\n    border-top-right-radius: $border-radius;\n    border-bottom-right-radius: $border-radius;\n  }\n}\n\n\n//\n// Striped\n//\n\n.progress-striped[value]::-webkit-progress-value {\n  @include gradient-striped();\n  background-size: $spacer-y $spacer-y;\n}\n.progress-striped[value]::-moz-progress-bar {\n  @include gradient-striped();\n  background-size: $spacer-y $spacer-y;\n}\n// IE9\n@media screen and (min-width:0\\0) {\n  .progress-bar-striped {\n    @include gradient-striped();\n    background-size: $spacer-y $spacer-y;\n  }\n}\n\n\n//\n// Animated\n//\n\n.progress-animated[value]::-webkit-progress-value {\n  animation: progress-bar-stripes 2s linear infinite;\n}\n.progress-animated[value]::-moz-progress-bar {\n  animation: progress-bar-stripes 2s linear infinite;\n}\n// IE9\n@media screen and (min-width:0\\0) {\n  .progress-animated .progress-bar-striped {\n    animation: progress-bar-stripes 2s linear infinite;\n  }\n}\n\n\n//\n// Variations\n//\n\n.progress-success {\n  @include progress-variant($progress-bar-success-bg);\n}\n.progress-info {\n  @include progress-variant($progress-bar-info-bg);\n}\n.progress-warning {\n  @include progress-variant($progress-bar-warning-bg);\n}\n.progress-danger {\n  @include progress-variant($progress-bar-danger-bg);\n}\n","// Gradients\n\n// Horizontal gradient, from left to right\n//\n// Creates two color stops, start and end, by specifying a color and position for each color stop.\n// Color stops are not available in IE9.\n@mixin gradient-horizontal($start-color: #555, $end-color: #333, $start-percent: 0%, $end-percent: 100%) {\n  background-image: linear-gradient(to right, $start-color $start-percent, $end-color $end-percent);\n  background-repeat: repeat-x;\n  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#{ie-hex-str($start-color)}', endColorstr='#{ie-hex-str($end-color)}', GradientType=1); // IE9\n}\n\n// Vertical gradient, from top to bottom\n//\n// Creates two color stops, start and end, by specifying a color and position for each color stop.\n// Color stops are not available in IE9.\n@mixin gradient-vertical($start-color: #555, $end-color: #333, $start-percent: 0%, $end-percent: 100%) {\n  background-image: linear-gradient(to bottom, $start-color $start-percent, $end-color $end-percent);\n  background-repeat: repeat-x;\n  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#{ie-hex-str($start-color)}', endColorstr='#{ie-hex-str($end-color)}', GradientType=0); // IE9\n}\n\n@mixin gradient-directional($start-color: #555, $end-color: #333, $deg: 45deg) {\n  background-repeat: repeat-x;\n  background-image: linear-gradient($deg, $start-color, $end-color);\n}\n@mixin gradient-horizontal-three-colors($start-color: #00b3ee, $mid-color: #7a43b6, $color-stop: 50%, $end-color: #c3325f) {\n  background-image: linear-gradient(to right, $start-color, $mid-color $color-stop, $end-color);\n  background-repeat: no-repeat;\n  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#{ie-hex-str($start-color)}', endColorstr='#{ie-hex-str($end-color)}', GradientType=1); // IE9 gets no color-stop at all for proper fallback\n}\n@mixin gradient-vertical-three-colors($start-color: #00b3ee, $mid-color: #7a43b6, $color-stop: 50%, $end-color: #c3325f) {\n  background-image: linear-gradient($start-color, $mid-color $color-stop, $end-color);\n  background-repeat: no-repeat;\n  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#{ie-hex-str($start-color)}', endColorstr='#{ie-hex-str($end-color)}', GradientType=0); // IE9 gets no color-stop at all for proper fallback\n}\n@mixin gradient-radial($inner-color: #555, $outer-color: #333) {\n  background-image: radial-gradient(circle, $inner-color, $outer-color);\n  background-repeat: no-repeat;\n}\n@mixin gradient-striped($color: rgba(255,255,255,.15), $angle: 45deg) {\n  background-image: linear-gradient($angle, $color 25%, transparent 25%, transparent 50%, $color 50%, $color 75%, transparent 75%, transparent);\n}","// Progress bars\n\n@mixin progress-variant($color) {\n  &[value]::-webkit-progress-value {\n    background-color: $color;\n  }\n\n  &[value]::-moz-progress-bar {\n    background-color: $color;\n  }\n\n  // IE9\n  @media screen and (min-width:0\\0) {\n    .progress-bar {\n      background-color: $color;\n    }\n  }\n}\n","@if $enable-flex {\n  .media {\n    display: flex;\n    margin-bottom: $spacer;\n  }\n  .media-body {\n    flex: 1;\n  }\n  .media-middle {\n    align-self: center;\n  }\n  .media-bottom {\n    align-self: flex-end;\n  }\n} @else {\n  .media {\n    margin-top: 15px;\n\n    &:first-child {\n      margin-top: 0;\n    }\n  }\n  .media,\n  .media-body {\n    overflow: hidden;\n    zoom: 1;\n  }\n  .media-body {\n    width: 10000px;\n  }\n  .media-left,\n  .media-right,\n  .media-body {\n    display: table-cell;\n    vertical-align: top;\n  }\n  .media-middle {\n    vertical-align: middle;\n  }\n  .media-bottom {\n    vertical-align: bottom;\n  }\n}\n\n\n//\n// Images/elements as the media anchor\n//\n\n.media-object {\n  display: block;\n\n  // Fix collapse in webkit from max-width: 100% and display: table-cell.\n  &.img-thumbnail {\n    max-width: none;\n  }\n}\n\n\n//\n// Alignment\n//\n\n.media-right {\n  padding-left: 10px;\n}\n\n.media-left {\n  padding-right: 10px;\n}\n\n\n//\n// Headings\n//\n\n.media-heading {\n  margin-top: 0;\n  margin-bottom: 5px;\n}\n\n\n//\n// Media list variation\n//\n\n.media-list {\n  padding-left: 0;\n  list-style: none;\n}\n","// Base class\n//\n// Easily usable on <ul>, <ol>, or <div>.\n\n.list-group {\n  // No need to set list-style: none; since .list-group-item is block level\n  padding-left: 0; // reset padding because ul and ol\n  margin-bottom: 0;\n}\n\n\n// Individual list items\n//\n// Use on `li`s or `div`s within the `.list-group` parent.\n\n.list-group-item {\n  position: relative;\n  display: block;\n  padding: .75rem 1.25rem;\n  // Place the border on the list items and negative margin up for better styling\n  margin-bottom: -$list-group-border-width;\n  background-color: $list-group-bg;\n  border: $list-group-border-width solid $list-group-border-color;\n\n  // Round the first and last items\n  &:first-child {\n    @include border-top-radius($list-group-border-radius);\n  }\n  &:last-child {\n    margin-bottom: 0;\n    @include border-bottom-radius($list-group-border-radius);\n  }\n}\n\n.list-group-flush {\n  .list-group-item {\n    border-width: $list-group-border-width 0;\n    border-radius: 0;\n  }\n\n  &:first-child {\n    .list-group-item:first-child {\n      border-top: 0;\n    }\n  }\n\n  &:last-child {\n    .list-group-item:last-child {\n      border-bottom: 0;\n    }\n  }\n}\n\n\n// Interactive list items\n//\n// Use anchor or button elements instead of `li`s or `div`s to create interactive\n// list items. Includes an extra `.active` modifier class for selected items.\n\na.list-group-item,\nbutton.list-group-item {\n  width: 100%;\n  color: $list-group-link-color;\n  text-align: inherit;\n\n  .list-group-item-heading {\n    color: $list-group-link-heading-color;\n  }\n\n  // Hover state\n  @include hover-focus {\n    color: $list-group-link-hover-color;\n    text-decoration: none;\n    background-color: $list-group-hover-bg;\n  }\n}\n\n.list-group-item {\n  // Disabled state\n  &.disabled {\n    @include plain-hover-focus {\n      color: $list-group-disabled-color;\n      cursor: $cursor-disabled;\n      background-color: $list-group-disabled-bg;\n\n      // Force color to inherit for custom content\n      .list-group-item-heading {\n        color: inherit;\n      }\n      .list-group-item-text {\n        color: $list-group-disabled-text-color;\n      }\n    }\n  }\n\n  // Active class on item itself, not parent\n  &.active {\n    @include plain-hover-focus {\n      z-index: 2; // Place active items above their siblings for proper border styling\n      color: $list-group-active-color;\n      background-color: $list-group-active-bg;\n      border-color: $list-group-active-border;\n\n      // Force color to inherit for custom content\n      .list-group-item-heading,\n      .list-group-item-heading > small,\n      .list-group-item-heading > .small {\n        color: inherit;\n      }\n      .list-group-item-text {\n        color: $list-group-active-text-color;\n      }\n    }\n  }\n}\n\n\n// Contextual variants\n//\n// Add modifier classes to change text and background color on individual items.\n// Organizationally, this must come after the `:hover` states.\n\n@include list-group-item-variant(success, $state-success-bg, $state-success-text);\n@include list-group-item-variant(info, $state-info-bg, $state-info-text);\n@include list-group-item-variant(warning, $state-warning-bg, $state-warning-text);\n@include list-group-item-variant(danger, $state-danger-bg, $state-danger-text);\n\n\n// Custom content options\n//\n// Extra classes for creating well-formatted content within `.list-group-item`s.\n\n.list-group-item-heading {\n  margin-top: 0;\n  margin-bottom: 5px;\n}\n.list-group-item-text {\n  margin-bottom: 0;\n  line-height: 1.3;\n}\n","// List Groups\n\n@mixin list-group-item-variant($state, $background, $color) {\n  .list-group-item-#{$state} {\n    color: $color;\n    background-color: $background;\n  }\n\n  a.list-group-item-#{$state},\n  button.list-group-item-#{$state} {\n    color: $color;\n\n    .list-group-item-heading {\n      color: inherit;\n    }\n\n    @include hover-focus {\n      color: $color;\n      background-color: darken($background, 5%);\n    }\n\n    &.active {\n      @include plain-hover-focus {\n        color: #fff;\n        background-color: $color;\n        border-color: $color;\n      }\n    }\n  }\n}\n","// Credit: Nicolas Gallagher and SUIT CSS.\n\n.embed-responsive {\n  position: relative;\n  display: block;\n  height: 0;\n  padding: 0;\n  overflow: hidden;\n\n  .embed-responsive-item,\n  iframe,\n  embed,\n  object,\n  video {\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    border: 0;\n  }\n}\n\n.embed-responsive-21by9 {\n  padding-bottom: percentage(9 / 21);\n}\n\n.embed-responsive-16by9 {\n  padding-bottom: percentage(9 / 16);\n}\n\n.embed-responsive-4by3 {\n  padding-bottom: percentage(3 / 4);\n}\n\n.embed-responsive-1by1 {\n  padding-bottom: percentage(1 / 1);\n}\n",".close {\n  float: right;\n  font-size: ($font-size-base * 1.5);\n  font-weight: $close-font-weight;\n  line-height: 1;\n  color: $close-color;\n  text-shadow: $close-text-shadow;\n  opacity: .2;\n\n  @include hover-focus {\n    color: $close-color;\n    text-decoration: none;\n    cursor: pointer;\n    opacity: .5;\n  }\n}\n\n// Additional properties for button version\n// iOS requires the button element instead of an anchor tag.\n// If you want the anchor version, it requires `href=\"#\"`.\n// See https://developer.mozilla.org/en-US/docs/Web/Events/click#Safari_Mobile\nbutton.close {\n  padding: 0;\n  cursor: pointer;\n  background: transparent;\n  border: 0;\n  -webkit-appearance: none;\n}\n","// .modal-open      - body class for killing the scroll\n// .modal           - container to scroll within\n// .modal-dialog    - positioning shell for the actual modal\n// .modal-content   - actual modal w/ bg and corners and shit\n\n\n// Kill the scroll on the body\n.modal-open {\n  overflow: hidden;\n}\n\n// Container that the modal scrolls within\n.modal {\n  position: fixed;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  z-index: $zindex-modal;\n  display: none;\n  overflow: hidden;\n  // Prevent Chrome on Windows from adding a focus outline. For details, see\n  // https://github.com/twbs/bootstrap/pull/10951.\n  outline: 0;\n  -webkit-overflow-scrolling: touch;\n\n  // When fading in the modal, animate it to slide down\n  &.fade .modal-dialog {\n    transition: transform .3s ease-out;\n    transform: translate(0, -25%);\n  }\n  &.in .modal-dialog { transform: translate(0, 0); }\n}\n.modal-open .modal {\n  overflow-x: hidden;\n  overflow-y: auto;\n}\n\n// Shell div to position the modal with bottom padding\n.modal-dialog {\n  position: relative;\n  width: auto;\n  margin: 10px;\n}\n\n// Actual modal\n.modal-content {\n  position: relative;\n  background-color: $modal-content-bg;\n  background-clip: padding-box;\n  border: 1px solid $modal-content-border-color;\n  border-radius: $border-radius-lg;\n  @include box-shadow(0 3px 9px rgba(0,0,0,.5));\n  // Remove focus outline from opened modal\n  outline: 0;\n}\n\n// Modal background\n.modal-backdrop {\n  position: fixed;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  z-index: $zindex-modal-bg;\n  background-color: $modal-backdrop-bg;\n\n  // Fade for backdrop\n  &.fade { opacity: 0; }\n  &.in { opacity: $modal-backdrop-opacity; }\n}\n\n// Modal header\n// Top section of the modal w/ title and dismiss\n.modal-header {\n  padding: $modal-title-padding;\n  border-bottom: 1px solid $modal-header-border-color;\n  @include clearfix;\n}\n// Close icon\n.modal-header .close {\n  margin-top: -2px;\n}\n\n// Title text within header\n.modal-title {\n  margin: 0;\n  line-height: $modal-title-line-height;\n}\n\n// Modal body\n// Where all modal content resides (sibling of .modal-header and .modal-footer)\n.modal-body {\n  position: relative;\n  padding: $modal-inner-padding;\n}\n\n// Footer (for actions)\n.modal-footer {\n  padding: $modal-inner-padding;\n  text-align: right; // right align buttons\n  border-top: 1px solid $modal-footer-border-color;\n  @include clearfix(); // clear it in case folks use .pull-* classes on buttons\n\n  // Properly space out buttons\n  .btn + .btn {\n    margin-bottom: 0; // account for input[type=\"submit\"] which gets the bottom margin like all other inputs\n    margin-left: 5px;\n  }\n  // but override that for button groups\n  .btn-group .btn + .btn {\n    margin-left: -1px;\n  }\n  // and override it for block buttons as well\n  .btn-block + .btn-block {\n    margin-left: 0;\n  }\n}\n\n// Measure scrollbar width for padding body during modal show/hide\n.modal-scrollbar-measure {\n  position: absolute;\n  top: -9999px;\n  width: 50px;\n  height: 50px;\n  overflow: scroll;\n}\n\n// Scale up the modal\n@include media-breakpoint-up(sm) {\n  // Automatically set modal's width for larger viewports\n  .modal-dialog {\n    width: $modal-md;\n    margin: 30px auto;\n  }\n  .modal-content {\n    @include box-shadow(0 5px 15px rgba(0,0,0,.5));\n  }\n\n  // Modal sizes\n  .modal-sm { width: $modal-sm; }\n}\n\n@include media-breakpoint-up(md) {\n  .modal-lg { width: $modal-lg; }\n}\n","// Base class\n.tooltip {\n  position: absolute;\n  z-index: $zindex-tooltip;\n  display: block;\n  // Our parent element can be arbitrary since tooltips are by default inserted as a sibling of their target element.\n  // So reset our font and text properties to avoid inheriting weird values.\n  @include reset-text();\n  font-size: $font-size-sm;\n  opacity: 0;\n\n  &.in { opacity: $tooltip-opacity; }\n\n  &.tooltip-top,\n  &.bs-tether-element-attached-bottom {\n    padding: $tooltip-arrow-width 0;\n    margin-top: -3px;\n\n    .tooltip-arrow {\n      bottom: 0;\n      left: 50%;\n      margin-left: -$tooltip-arrow-width;\n      border-width: $tooltip-arrow-width $tooltip-arrow-width 0;\n      border-top-color: $tooltip-arrow-color;\n    }\n  }\n  &.tooltip-right,\n  &.bs-tether-element-attached-left {\n    padding: 0 $tooltip-arrow-width;\n    margin-left: 3px;\n\n    .tooltip-arrow {\n      top: 50%;\n      left: 0;\n      margin-top: -$tooltip-arrow-width;\n      border-width: $tooltip-arrow-width $tooltip-arrow-width $tooltip-arrow-width 0;\n      border-right-color: $tooltip-arrow-color;\n    }\n  }\n  &.tooltip-bottom,\n  &.bs-tether-element-attached-top {\n    padding: $tooltip-arrow-width 0;\n    margin-top: 3px;\n\n    .tooltip-arrow {\n      top: 0;\n      left: 50%;\n      margin-left: -$tooltip-arrow-width;\n      border-width: 0 $tooltip-arrow-width $tooltip-arrow-width;\n      border-bottom-color: $tooltip-arrow-color;\n    }\n  }\n  &.tooltip-left,\n  &.bs-tether-element-attached-right {\n    padding: 0 $tooltip-arrow-width;\n    margin-left: -3px;\n\n    .tooltip-arrow {\n      top: 50%;\n      right: 0;\n      margin-top: -$tooltip-arrow-width;\n      border-width: $tooltip-arrow-width 0 $tooltip-arrow-width $tooltip-arrow-width;\n      border-left-color: $tooltip-arrow-color;\n    }\n  }\n}\n\n// Wrapper for the tooltip content\n.tooltip-inner {\n  max-width: $tooltip-max-width;\n  padding: 3px 8px;\n  color: $tooltip-color;\n  text-align: center;\n  background-color: $tooltip-bg;\n  @include border-radius($border-radius);\n}\n\n// Arrows\n.tooltip-arrow {\n  position: absolute;\n  width: 0;\n  height: 0;\n  border-color: transparent;\n  border-style: solid;\n}\n","@mixin reset-text {\n  font-family: $font-family-base;\n  // We deliberately do NOT reset font-size.\n  font-style: normal;\n  font-weight: normal;\n  letter-spacing: normal;\n  line-break: auto;\n  line-height: $line-height;\n  text-align: left; // Fallback for where `start` is not supported\n  text-align: start;\n  text-decoration: none;\n  text-shadow: none;\n  text-transform: none;\n  white-space: normal;\n  word-break: normal;\n  word-spacing: normal;\n  word-wrap: normal;\n}\n",".popover {\n  position: absolute;\n  top: 0;\n  left: 0;\n  z-index: $zindex-popover;\n  display: block;\n  max-width: $popover-max-width;\n  padding: 1px;\n  // Our parent element can be arbitrary since tooltips are by default inserted as a sibling of their target element.\n  // So reset our font and text properties to avoid inheriting weird values.\n  @include reset-text();\n  font-size: $font-size-sm;\n  background-color: $popover-bg;\n  background-clip: padding-box;\n  border: $popover-border-width solid $popover-border-color;\n  @include border-radius($border-radius-lg);\n  @include box-shadow(0 5px 10px rgba(0,0,0,.2));\n\n\n  // Popover directions\n\n  &.popover-top,\n  &.bs-tether-element-attached-bottom {\n    margin-top: -$popover-arrow-width;\n\n    .popover-arrow {\n      bottom: -$popover-arrow-outer-width;\n      left: 50%;\n      margin-left: -$popover-arrow-outer-width;\n      border-top-color: $popover-arrow-outer-color;\n      border-bottom-width: 0;\n      &::after {\n        bottom: 1px;\n        margin-left: -$popover-arrow-width;\n        content: \"\";\n        border-top-color: $popover-arrow-color;\n        border-bottom-width: 0;\n      }\n    }\n  }\n\n  &.popover-right,\n  &.bs-tether-element-attached-left {\n    margin-left: $popover-arrow-width;\n\n    .popover-arrow {\n      top: 50%;\n      left: -$popover-arrow-outer-width;\n      margin-top: -$popover-arrow-outer-width;\n      border-right-color: $popover-arrow-outer-color;\n      border-left-width: 0;\n      &::after {\n        bottom: -$popover-arrow-width;\n        left: 1px;\n        content: \"\";\n        border-right-color: $popover-arrow-color;\n        border-left-width: 0;\n      }\n    }\n  }\n\n  &.popover-bottom,\n  &.bs-tether-element-attached-top {\n    margin-top: $popover-arrow-width;\n\n    .popover-arrow {\n      top: -$popover-arrow-outer-width;\n      left: 50%;\n      margin-left: -$popover-arrow-outer-width;\n      border-top-width: 0;\n      border-bottom-color: $popover-arrow-outer-color;\n      &::after {\n        top: 1px;\n        margin-left: -$popover-arrow-width;\n        content: \"\";\n        border-top-width: 0;\n        border-bottom-color: $popover-arrow-color;\n      }\n    }\n  }\n\n  &.popover-left,\n  &.bs-tether-element-attached-right {\n    margin-left: -$popover-arrow-width;\n\n    .popover-arrow {\n      top: 50%;\n      right: -$popover-arrow-outer-width;\n      margin-top: -$popover-arrow-outer-width;\n      border-right-width: 0;\n      border-left-color: $popover-arrow-outer-color;\n      &::after {\n        right: 1px;\n        bottom: -$popover-arrow-width;\n        content: \"\";\n        border-right-width: 0;\n        border-left-color: $popover-arrow-color;\n      }\n    }\n  }\n}\n\n\n// Offset the popover to account for the popover arrow\n.popover-title {\n  padding: 8px 14px;\n  margin: 0; // reset heading margin\n  font-size: $font-size-base;\n  background-color: $popover-title-bg;\n  border-bottom: $popover-border-width solid darken($popover-title-bg, 5%);\n  @include border-radius(($border-radius-lg - 1) ($border-radius-lg - 1) 0 0);\n}\n\n.popover-content {\n  padding: 9px 14px;\n}\n\n\n// Arrows\n//\n// .popover-arrow is outer, .popover-arrow::after is inner\n\n.popover-arrow {\n  &,\n  &::after {\n    position: absolute;\n    display: block;\n    width: 0;\n    height: 0;\n    border-color: transparent;\n    border-style: solid;\n  }\n}\n.popover-arrow {\n  border-width: $popover-arrow-outer-width;\n}\n.popover-arrow::after {\n  content: \"\";\n  border-width: $popover-arrow-width;\n}\n","// Wrapper for the slide container and indicators\n.carousel {\n  position: relative;\n}\n\n.carousel-inner {\n  position: relative;\n  width: 100%;\n  overflow: hidden;\n\n  > .carousel-item {\n    position: relative;\n    display: none;\n    transition: .6s ease-in-out left;\n\n    // Account for jankitude on images\n    > img,\n    > a > img {\n      @extend .img-fluid;\n      line-height: 1;\n    }\n\n    // WebKit CSS3 transforms for supported devices\n    @media all and (transform-3d), (-webkit-transform-3d) {\n      transition: transform .6s ease-in-out;\n      backface-visibility: hidden;\n      perspective: 1000px;\n\n      &.next,\n      &.active.right {\n        left: 0;\n        transform: translate3d(100%, 0, 0);\n      }\n      &.prev,\n      &.active.left {\n        left: 0;\n        transform: translate3d(-100%, 0, 0);\n      }\n      &.next.left,\n      &.prev.right,\n      &.active {\n        left: 0;\n        transform: translate3d(0, 0, 0);\n      }\n    }\n  }\n\n  > .active,\n  > .next,\n  > .prev {\n    display: block;\n  }\n\n  > .active {\n    left: 0;\n  }\n\n  > .next,\n  > .prev {\n    position: absolute;\n    top: 0;\n    width: 100%;\n  }\n\n  > .next {\n    left: 100%;\n  }\n  > .prev {\n    left: -100%;\n  }\n  > .next.left,\n  > .prev.right {\n    left: 0;\n  }\n\n  > .active.left {\n    left: -100%;\n  }\n  > .active.right {\n    left: 100%;\n  }\n}\n\n\n//\n// Left/right controls for nav\n//\n\n.carousel-control {\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  width: $carousel-control-width;\n  font-size: $carousel-control-font-size;\n  color: $carousel-control-color;\n  text-align: center;\n  text-shadow: $carousel-text-shadow;\n  opacity: $carousel-control-opacity;\n  // We can't have this transition here because WebKit cancels the carousel\n  // animation if you trip this while in the middle of another animation.\n\n  // Set gradients for backgrounds\n  &.left {\n    @include gradient-horizontal($start-color: rgba(0,0,0,.5), $end-color: rgba(0,0,0,.0001));\n  }\n  &.right {\n    right: 0;\n    left: auto;\n    @include gradient-horizontal($start-color: rgba(0,0,0,.0001), $end-color: rgba(0,0,0,.5));\n  }\n\n  // Hover/focus state\n  @include hover-focus {\n    color: $carousel-control-color;\n    text-decoration: none;\n    outline: 0;\n    opacity: .9;\n  }\n\n  // Toggles\n  .icon-prev,\n  .icon-next {\n    position: absolute;\n    top: 50%;\n    z-index: 5;\n    display: inline-block;\n    width: 20px;\n    height: 20px;\n    margin-top: -10px;\n    font-family: serif;\n    line-height: 1;\n  }\n  .icon-prev {\n    left: 50%;\n    margin-left: -10px;\n  }\n  .icon-next {\n    right: 50%;\n    margin-right: -10px;\n  }\n\n  .icon-prev {\n    &::before {\n      content: \"\\2039\";// SINGLE LEFT-POINTING ANGLE QUOTATION MARK (U+2039)\n    }\n  }\n  .icon-next {\n    &::before {\n      content: \"\\203a\";// SINGLE RIGHT-POINTING ANGLE QUOTATION MARK (U+203A)\n    }\n  }\n}\n\n\n// Optional indicator pips\n//\n// Add an unordered list with the following class and add a list item for each\n// slide your carousel holds.\n\n.carousel-indicators {\n  position: absolute;\n  bottom: 10px;\n  left: 50%;\n  z-index: 15;\n  width: 60%;\n  padding-left: 0;\n  margin-left: -30%;\n  text-align: center;\n  list-style: none;\n\n  li {\n    display: inline-block;\n    width: 10px;\n    height: 10px;\n    margin: 1px;\n    text-indent: -999px;\n    cursor: pointer;\n    // IE9 hack for event handling\n    //\n    // Internet Explorer 9 does not properly handle clicks on elements with a `background-color` of `transparent`,\n    // so we use `rgba(0,0,0,0)` instead since it's a non-buggy equivalent.\n    // See https://developer.mozilla.org/en-US/docs/Web/Events/click#Internet_Explorer\n    background-color: rgba(0,0,0,0); // IE9\n    border: 1px solid $carousel-indicator-border-color;\n    border-radius: 10px;\n  }\n  .active {\n    width: 12px;\n    height: 12px;\n    margin: 0;\n    background-color: $carousel-indicator-active-bg;\n  }\n}\n\n\n// Optional captions\n//\n// Hidden by default for smaller viewports.\n\n.carousel-caption {\n  position: absolute;\n  right: 15%;\n  bottom: 20px;\n  left: 15%;\n  z-index: 10;\n  padding-top: 20px;\n  padding-bottom: 20px;\n  color: $carousel-caption-color;\n  text-align: center;\n  text-shadow: $carousel-text-shadow;\n\n  .btn {\n    text-shadow: none; // No shadow for button elements in carousel-caption\n  }\n}\n\n\n//\n// Responsive variations\n//\n\n@include media-breakpoint-up(sm) {\n  // Scale up the controls a smidge\n  .carousel-control {\n    .icon-prev,\n    .icon-next {\n      width: 30px;\n      height: 30px;\n      margin-top: -15px;\n      font-size: 30px;\n    }\n    .icon-prev {\n      margin-left: -15px;\n    }\n    .icon-next {\n      margin-right: -15px;\n    }\n  }\n\n  // Show and left align the captions\n  .carousel-caption {\n    right: 20%;\n    left: 20%;\n    padding-bottom: 30px;\n  }\n\n  // Move up the indicators\n  .carousel-indicators {\n    bottom: 20px;\n  }\n}\n","//\n// Floats\n//\n\n.clearfix {\n  @include clearfix();\n}\n\n.center-block {\n  @include center-block();\n}\n\n@each $breakpoint in map-keys($grid-breakpoints) {\n  @include media-breakpoint-up($breakpoint) {\n    .pull-#{$breakpoint}-left {\n      @include pull-left();\n    }\n    .pull-#{$breakpoint}-right {\n      @include pull-right();\n    }\n    .pull-#{$breakpoint}-none {\n      float: none !important;\n    }\n  }\n}\n\n\n//\n// Screenreaders\n//\n\n.sr-only {\n  @include sr-only();\n}\n\n.sr-only-focusable {\n  @include sr-only-focusable();\n}\n\n.invisible {\n  visibility: hidden !important;\n}\n\n.text-hide {\n  @include text-hide();\n}\n\n\n//\n// Text\n//\n\n// Alignment\n\n.text-justify        { text-align: justify !important; }\n.text-nowrap         { white-space: nowrap !important; }\n.text-truncate       { @include text-truncate; }\n\n// Responsive alignment\n\n@each $breakpoint in map-keys($grid-breakpoints) {\n  @include media-breakpoint-up($breakpoint) {\n    .text-#{$breakpoint}-left   { text-align: left !important; }\n    .text-#{$breakpoint}-right  { text-align: right !important; }\n    .text-#{$breakpoint}-center { text-align: center !important; }\n  }\n}\n\n// Transformation\n\n.text-lowercase      { text-transform: lowercase !important; }\n.text-uppercase      { text-transform: uppercase !important; }\n.text-capitalize     { text-transform: capitalize !important; }\n\n// Weight and italics\n\n.font-weight-normal  { font-weight: normal; }\n.font-weight-bold    { font-weight: bold; }\n.font-italic         { font-style: italic; }\n\n// Contextual colors\n\n.text-muted {\n  color: $text-muted;\n}\n\n@include text-emphasis-variant('.text-primary', $brand-primary);\n\n@include text-emphasis-variant('.text-success', $brand-success);\n\n@include text-emphasis-variant('.text-info', $brand-info);\n\n@include text-emphasis-variant('.text-warning', $brand-warning);\n\n@include text-emphasis-variant('.text-danger', $brand-danger);\n","// Center-align a block level element\n\n@mixin center-block() {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n}\n","@mixin pull-left {\n  float: left !important;\n}\n@mixin pull-right {\n  float: right !important;\n}\n","// Only display content to screen readers\n//\n// See: http://a11yproject.com/posts/how-to-hide-content/\n\n@mixin sr-only {\n  position: absolute;\n  width: 1px;\n  height: 1px;\n  padding: 0;\n  margin: -1px;\n  overflow: hidden;\n  clip: rect(0,0,0,0);\n  border: 0;\n}\n\n// Use in conjunction with .sr-only to only display content when it's focused.\n//\n// Useful for \"Skip to main content\" links; see http://www.w3.org/TR/2013/NOTE-WCAG20-TECHS-20130905/G1\n//\n// Credit: HTML5 Boilerplate\n\n@mixin sr-only-focusable {\n  &:active,\n  &:focus {\n    position: static;\n    width: auto;\n    height: auto;\n    margin: 0;\n    overflow: visible;\n    clip: auto;\n  }\n}\n","// CSS image replacement\n@mixin text-hide() {\n  font: \"0/0\" a;\n  color: transparent;\n  text-shadow: none;\n  background-color: transparent;\n  border: 0;\n}\n","// Text truncate\n// Requires inline-block or block for proper styling\n\n@mixin text-truncate() {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}","// Typography\n\n@mixin text-emphasis-variant($parent, $color) {\n  #{$parent} {\n    color: $color !important;\n  }\n  a#{$parent} {\n    @include hover-focus {\n      color: darken($color, 10%);\n    }\n  }\n}\n","//\n// Contextual backgrounds\n//\n\n// Inverse\n// TODO: redo this as a proper class\n.bg-inverse {\n  color: $gray-lighter;\n  background-color: $gray-dark;\n}\n\n.bg-faded {\n  background-color: $gray-lightest;\n}\n\n@include bg-variant('.bg-primary', $brand-primary);\n\n@include bg-variant('.bg-success', $brand-success);\n\n@include bg-variant('.bg-info', $brand-info);\n\n@include bg-variant('.bg-warning', $brand-warning);\n\n@include bg-variant('.bg-danger', $brand-danger);\n","// Contextual backgrounds\n\n@mixin bg-variant($parent, $color) {\n  #{$parent} {\n    color: #fff !important;\n    background-color: $color !important;\n  }\n  a#{$parent} {\n    @include hover-focus {\n      background-color: darken($color, 10%);\n    }\n  }\n}\n","// Margin and Padding\n\n.m-x-auto {\n  margin-right: auto !important;\n  margin-left:  auto !important;\n}\n\n@each $prop, $abbrev in (margin: m, padding: p) {\n  @each $size, $lengths in $spacers {\n    $length-x:   map-get($lengths, x);\n    $length-y:   map-get($lengths, y);\n\n    .#{$abbrev}-a-#{$size} { #{$prop}:        $length-y $length-x !important; } // a = All sides\n    .#{$abbrev}-t-#{$size} { #{$prop}-top:    $length-y !important; }\n    .#{$abbrev}-r-#{$size} { #{$prop}-right:  $length-x !important; }\n    .#{$abbrev}-b-#{$size} { #{$prop}-bottom: $length-y !important; }\n    .#{$abbrev}-l-#{$size} { #{$prop}-left:   $length-x !important; }\n\n    // Axes\n    .#{$abbrev}-x-#{$size} {\n      #{$prop}-right:  $length-x !important;\n      #{$prop}-left:   $length-x !important;\n    }\n    .#{$abbrev}-y-#{$size} {\n      #{$prop}-top:    $length-y !important;\n      #{$prop}-bottom: $length-y !important;\n    }\n  }\n}\n\n// Positioning\n\n.pos-f-t {\n  position: fixed;\n  top: 0;\n  right: 0;\n  left: 0;\n  z-index: $zindex-navbar-fixed;\n}\n","//\n// Responsive utilities\n//\n\n@each $bp in map-keys($grid-breakpoints) {\n  .hidden-#{$bp}-up {\n    @include media-breakpoint-up($bp) {\n      display: none !important;\n    }\n  }\n  .hidden-#{$bp}-down {\n    @include media-breakpoint-down($bp) {\n      display: none !important;\n    }\n  }\n}\n\n\n// Print utilities\n//\n// Media queries are placed on the inside to be mixin-friendly.\n\n.visible-print-block {\n  display: none !important;\n\n  @media print {\n    display: block !important;\n  }\n}\n.visible-print-inline {\n  display: none !important;\n\n  @media print {\n    display: inline !important;\n  }\n}\n.visible-print-inline-block {\n  display: none !important;\n\n  @media print {\n    display: inline-block !important;\n  }\n}\n\n.hidden-print {\n  @media print {\n    display: none !important;\n  }\n}\n","//\r\n// Typography\r\n// --------------------------------------------------\r\n\r\n.blockquote-sm{\r\n  padding: 3px 15px;\r\n  font-size: $font-size-base;\r\n}\r\n\r\nblockquote{\r\n  font-size: $font-size-lg;\r\n\r\n  footer {\r\n    font-size: 80%;\r\n    &:after {\r\n      content: '\\00A0 \\2014';\r\n    }\r\n  }\r\n}\r\n// Headings\r\n// -------------------------\r\n\r\nh1, h2, h3, h4, h5, h6,\r\n.h1, .h2, .h3, .h4, .h5, .h6{\r\n  small,\r\n  .small {\r\n    font-weight: $headings-font-weight;\r\n    font-size: 75%;\r\n    color: $text-muted;\r\n  }\r\n}\r\n\r\n//\r\n// Panels\r\n// --------------------------------------------------\r\n\r\n// Base class\r\n.panel {\r\n  margin-bottom: $line-height-computed;\r\n  background-color: $panel-bg;\r\n  border: none;\r\n}\r\n\r\n// Panel contents\r\n.panel-body {\r\n  padding: $panel-body-padding;\r\n  @include clearfix;\r\n}\r\n\r\n// Optional heading\r\n.panel-heading {\r\n  padding: 13px 15px;\r\n  border-bottom: 1px solid transparent;\r\n  @include border-top-radius(($border-radius));\r\n\r\n  > .dropdown .dropdown-toggle {\r\n    color: inherit;\r\n  }\r\n}\r\n\r\n// Within heading, strip any `h*` tag of its default margins for spacing.\r\n.panel-title>a.collapsed>i {\r\n  transform: rotate(180deg);\r\n  transition: transform 0.35s ease;\r\n}\r\n\r\n.panel-title>a>i {\r\n  transition: transform 0.35s ease;\r\n}\r\n\r\n.panel-title {\r\n  margin-top: 0;\r\n  margin-bottom: 0;\r\n  font-size: $font-size-base;\r\n  color: inherit;\r\n\r\n  > a,\r\n  > small,\r\n  > .small,\r\n  > small > a,\r\n  > .small > a {\r\n    color: inherit;\r\n    display: block;\r\n  }\r\n}\r\n\r\nsmall, .small {\r\n  font-size: 85%;\r\n}\r\n\r\n// Collapsable panels (aka, accordion)\r\n//\r\n// Wrap a series of panels in `.panel-group` to turn them into an accordion with\r\n// the help of our collapse JavaScript plugin.\r\n\r\n.panel-group {\r\n  margin-bottom: $line-height-computed;\r\n\r\n  // Tighten up margin so it's only between panels\r\n  .panel {\r\n    margin-bottom: 0;\r\n    border-radius: $border-radius;\r\n\r\n    + .panel {\r\n      margin-top: 5px;\r\n    }\r\n  }\r\n\r\n  .panel-heading {\r\n    border-bottom: 0;\r\n\r\n    + .panel-collapse > .panel-body,\r\n    + .panel-collapse > .list-group {\r\n      border-top: 1px solid $list-group-border-color;\r\n    }\r\n\r\n    .collapsed {\r\n      &,\r\n      i {\r\n        opacity: 0.6;\r\n      }\r\n\r\n      &:hover,\r\n      &:hover > i{\r\n        opacity: 1;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n\r\n// Contextual variations\r\n.panel-default {\r\n  @include panel-variant($panel-default-border, $panel-default-text, $panel-default-heading-bg, $panel-default-border);\r\n}\r\n\r\n//\r\n// Forms\r\n// --------------------------------------------------\r\n\r\n\r\n.form-control {\r\n  font-weight: $font-weight-normal;\r\n  padding: $padding-base-vertical $padding-base-horizontal;\r\n  @include box-shadow(none);\r\n  &:focus{\r\n    @include box-shadow(none);\r\n  }\r\n}\r\n\r\n.form-control-lg {\r\n  padding: $padding-large-vertical $padding-large-horizontal;\r\n}\r\n\r\n.form-control-sm {\r\n  padding: $padding-small-vertical $padding-small-horizontal;\r\n}\r\n\r\n.input-group-btn>.btn {\r\n  padding-top: $padding-base-vertical;\r\n  padding-bottom: $padding-base-vertical;\r\n}\r\n\r\n.btn-group.bootstrap-select > .btn {\r\n  padding-top: $padding-base-vertical;\r\n  padding-bottom: $padding-base-vertical;\r\n}\r\n\r\n.btn-lg, .btn-group-lg>.btn {\r\n padding: $btn-padding-y-lg $btn-padding-x-lg !important;\r\n}\r\n\r\nlabel {\r\n  font-weight: $font-weight-normal;\r\n}\r\n\r\n.help-block{\r\n  margin-top: 5px;\r\n  font-size: $font-size-smaller;\r\n  color: $text-muted;\r\n  display: block;\r\n}\r\n\r\nlegend {\r\n  display: block;\r\n  width: 100%;\r\n  padding: 0;\r\n  margin-bottom: $line-height-computed;\r\n  line-height: inherit;\r\n  color: $legend-color;\r\n  border: 0;\r\n  border-bottom: 1px solid #e5e5e5;\r\n  font-size: 15px;\r\n  font-weight: $font-weight-normal;\r\n}\r\n\r\n.form-group {\r\n  margin-bottom: $line-height-computed;\r\n}\r\n\r\n.input-group-addon{\r\n  &.bg-primary{\r\n    border-color: darken($brand-primary, 10%);\r\n  }\r\n  &.bg-success{\r\n    border-color: darken($state-success-bg, 10%);\r\n  }\r\n  &.bg-info{\r\n    border-color: darken($state-info-bg, 10%);\r\n  }\r\n  &.bg-warning{\r\n    border-color: darken($state-warning-bg, 10%);\r\n  }\r\n  &.bg-danger{\r\n    border-color: darken($state-danger-bg, 10%);\r\n  }\r\n}\r\n\r\n.form-horizontal.form-label-left {\r\n  @include media-breakpoint-up(md){\r\n    .control-label {\r\n      text-align: left;\r\n    }\r\n  }\r\n}\r\n\r\n// Feedback states\r\n.has-success {\r\n  @include form-control-validation($brand-success);\r\n}\r\n.has-warning {\r\n  @include form-control-validation($brand-warning);\r\n}\r\n.has-error {\r\n  @include form-control-validation($brand-danger);\r\n}\r\n\r\n//turn off shadow\r\n.has-success,\r\n.has-warning,\r\n.has-error {\r\n  .form-control {\r\n    &:focus {\r\n      @include box-shadow(none);\r\n    }\r\n  }\r\n}\r\n\r\n.input-rounded{\r\n  border-radius: $border-radius;\r\n}\r\n\r\n\r\n// Checkboxes and radios\r\n//\r\n\r\n.radio,\r\n.checkbox {\r\n  padding-left: 20px;\r\n\r\n  label {\r\n    margin-bottom: 5px;\r\n    min-height: 0;\r\n  }\r\n}\r\n\r\n//\r\n// Tables\r\n// --------------------------------------------------\r\n\r\n.table td > .abc-checkbox,\r\n.table th > .abc-checkbox{\r\n  position: relative;\r\n  top: -8px;\r\n  margin-top: 0;\r\n  margin-bottom: 0;\r\n  padding-left: 0;\r\n  > label:before,\r\n  > label:after{\r\n    margin-left: 0;\r\n  }\r\n}\r\n\r\n.table > thead > tr > th {\r\n  border-top: none;\r\n}\r\n\r\n.table > thead > tr > th{\r\n  font-weight: $font-weight-semi-bold;\r\n  text-transform: uppercase;\r\n  border-bottom-width: 1px;\r\n}\r\n\r\n.table-bordered th{\r\n  background-color: $gray-lighter;\r\n}\r\n\r\n.table-lg {\r\n  > thead,\r\n  > tbody,\r\n  > tfoot {\r\n    > tr {\r\n      > th,\r\n      > td {\r\n        padding: 10px;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n.table-sm {\r\n  > thead,\r\n  > tbody,\r\n  > tfoot {\r\n    > tr {\r\n      > th,\r\n      > td {\r\n        padding: 6px;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n.table-hover > tbody > tr:hover .progress{\r\n  background-color: $white;\r\n}\r\n\r\n.table th, .table td {\r\n  padding: 0.55rem;\r\n}\r\n\r\n//\r\n// Dropdown menus\r\n// --------------------------------------------------\r\n\r\n.dropdown-toggle {\r\n  &:after {\r\n    content: none;\r\n  }\r\n}\r\n\r\n.dropdown-menu{\r\n  font-size: $font-size-mini;\r\n  border-radius: 0;\r\n\r\n  & > li > a{\r\n    padding-top: 5px;\r\n    padding-bottom: 5px;\r\n  }\r\n}\r\n\r\n.caret {\r\n  display: inline-block;\r\n  width: 0;\r\n  height: 0;\r\n  margin-left: 2px;\r\n  vertical-align: middle;\r\n  border-top:   4px dashed;\r\n  border-top:   4px solid \\9; // IE8\r\n  border-right: 4px solid transparent;\r\n  border-left:  4px solid transparent;\r\n}\r\n//\r\n// Navs\r\n// --------------------------------------------------\r\n\r\n.nav-pills .nav-link {\r\n  border-radius: 0;\r\n}\r\n\r\n.nav-pills .nav-item + .nav-item {\r\n  margin-left: 0;\r\n}\r\n\r\n.nav-tabs {\r\n  & .nav-item+.nav-item { margin-left: 0}\r\n  border-bottom: none;\r\n  background-color: $gray-semi-lighter;\r\n  border-top-left-radius: $border-radius;\r\n  border-top-right-radius: $border-radius;\r\n  > .nav-item {\r\n    margin-bottom: -2px;\r\n    > .nav-link {\r\n      padding: 12px 18px;\r\n      border: none;\r\n      color: $text-muted;\r\n\r\n      .label {\r\n        margin-bottom: -2px;\r\n      }\r\n\r\n      @include transition(color .15s ease-in-out);\r\n      &:hover {\r\n        background-color: transparent;\r\n        color: $text-color;\r\n      }\r\n    }\r\n\r\n    .nav-link.open {\r\n      &,\r\n      &:hover,\r\n      &:focus{\r\n        background-color: $nav-tabs-active-link-hover-bg;\r\n        color: $nav-tabs-active-link-hover-color;\r\n      }\r\n    }\r\n\r\n    .nav-link.active {\r\n      &,\r\n      &:hover,\r\n      &:focus {\r\n        background-color: $nav-tabs-active-link-hover-bg;\r\n        color: $nav-tabs-active-link-hover-color;\r\n        border: none;\r\n        @include box-shadow(1px 1px 2px #ccc);\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n.tab-content {\r\n  position: relative;\r\n  z-index: 1;\r\n  background-color: $white;\r\n  > .tab-pane {\r\n    padding: $line-height-computed*2 $line-height-computed*2;\r\n  }\r\n}\r\n\r\n// LEFT & RIGHT\r\n// ------------\r\n\r\n// TABBABLE\r\n// --------\r\n\r\n\r\n// COMMON STYLES\r\n// -------------\r\n\r\n.tabbable {\r\n  @include clearfix();\r\n}\r\n.tab-content {\r\n  overflow: auto; // prevent content from running below tabs\r\n}\r\n\r\n// Remove border on bottom, left, right\r\n.tabs-below > .nav-tabs,\r\n.tabs-right > .nav-tabs,\r\n.tabs-left > .nav-tabs {\r\n  border-bottom: 0;\r\n}\r\n\r\n// Show/hide tabbable areas\r\n.tab-content > .tab-pane,\r\n.pill-content > .pill-pane {\r\n  display: none;\r\n}\r\n.tab-content > .active,\r\n.pill-content > .active {\r\n  display: block;\r\n}\r\n\r\n// LEFT & RIGHT\r\n// ------------\r\n\r\n// Common styles\r\n.tabs-left > .nav-tabs > li,\r\n.tabs-right > .nav-tabs > li {\r\n  float: none;\r\n}\r\n.tabs-left > .nav-tabs > li > a,\r\n.tabs-right > .nav-tabs > li > a {\r\n  min-width: 74px;\r\n  margin-right: 0;\r\n  margin-bottom: 3px;\r\n}\r\n\r\n.tabs-left > .nav-tabs > li:last-child > a,\r\n.tabs-right > .nav-tabs > li:last-child > a{\r\n  margin-bottom: 0;\r\n}\r\n\r\n// Tabs on the left\r\n.tabs-left > .nav-tabs {\r\n  float: left;\r\n  border-top-right-radius: 0;\r\n}\r\n.tabs-left > .nav-tabs > li > a {\r\n  margin-right: -1px;\r\n  @include border-radius(4px 0 0 4px);\r\n}\r\n\r\n// Tabs on the right\r\n.tabs-right > .nav-tabs {\r\n  float: right;\r\n  border-top-left-radius: 0;\r\n}\r\n.tabs-right > .nav-tabs > li > a {\r\n  margin-left: -1px;\r\n  @include border-radius(0 4px 4px 0);\r\n}\r\n\r\n.tabs-right > .nav-tabs > li.active > a{\r\n  @include box-shadow(-1px 1px 2px #ccc);\r\n}\r\n\r\n\r\n//\r\n// Navbars\r\n// --------------------------------------------------\r\n\r\n.navbar {\r\n  border: none;\r\n  font-size: $navbar-font-size;\r\n  font-weight: 500;\r\n  min-height: 50px;\r\n  margin-bottom: 0;\r\n  padding: 0;\r\n\r\n  h5{\r\n    font-size: $navbar-font-size;\r\n  }\r\n\r\n  .deemphasize{\r\n    font-size: $font-size-sm;\r\n  }\r\n}\r\n\r\n.navbar-form{\r\n  .form-control{\r\n    font-size: $navbar-font-size;\r\n    padding: 0.6rem 0.85rem;\r\n\r\n  }\r\n\r\n  .input-group{\r\n    width: 245px;\r\n  }\r\n\r\n  .input-group-addon{\r\n    color: $navbar-dashboard-link-color;\r\n  }\r\n}\r\n\r\n.container,\r\n.container-fluid {\r\n  > .navbar-header,\r\n  > .navbar-collapse {\r\n    @media (min-width: map_get($grid-breakpoints, md)) {\r\n      margin-right: -$navbar-padding-horizontal;\r\n      margin-left:  -$navbar-padding-horizontal;\r\n    }\r\n  }\r\n}\r\n\r\n.navbar-header > .navbar-nav{\r\n  float: left;\r\n\r\n  &.navbar-right{\r\n    float: right;\r\n\r\n    @include media-breakpoint-down(sm) {\r\n      padding-top: $navbar-padding-horizontal;\r\n\r\n      > li > a {\r\n        padding-right: 0;\r\n      }\r\n    }\r\n  }\r\n\r\n  margin-left: calc(#{$navbar-padding-horizontal} + 10px);\r\n  margin-right: $navbar-padding-horizontal;\r\n\r\n  @include media-breakpoint-down(sm) {\r\n    margin-left: $navbar-padding-vertical;\r\n\r\n    + .navbar-brand{\r\n      height: 60px;\r\n      line-height: 30px;\r\n    }\r\n  }\r\n\r\n  > li {\r\n    float: left;\r\n    > a {\r\n      padding: 16px $navbar-padding-vertical $navbar-padding-horizontal $navbar-padding-vertical;\r\n\r\n\r\n    }\r\n  }\r\n\r\n  li:nth-child(3){\r\n    margin-left: 0;\r\n  }\r\n}\r\n\r\n@media (min-width: map_get($grid-breakpoints, md)) {\r\n  .navbar-nav.navbar-right:last-child {\r\n    margin-right: 0;\r\n  }\r\n}\r\n\r\n.navbar-collapse {\r\n  overflow-x: visible;\r\n  padding-right: $navbar-padding-horizontal;\r\n  padding-left:  $navbar-padding-horizontal;\r\n  border-top: 1px solid transparent;\r\n  box-shadow: inset 0 1px 0 rgba(255,255,255,.1);\r\n  @include clearfix;\r\n\r\n  &.in {\r\n    overflow-y: auto;\r\n  }\r\n\r\n  @media (min-width: map_get($grid-breakpoints, md)) {\r\n    width: auto;\r\n    border-top: 0;\r\n    box-shadow: none;\r\n\r\n    &.collapse {\r\n      display: block !important;\r\n      height: auto !important;\r\n      padding-bottom: 0; // Override default setting\r\n      overflow: visible !important;\r\n    }\r\n\r\n    &.in {\r\n      overflow-y: visible;\r\n    }\r\n\r\n    // Undo the collapse side padding for navbars with containers to ensure\r\n    // alignment of right-aligned contents.\r\n    .navbar-fixed-top &,\r\n    .navbar-static-top &,\r\n    .navbar-fixed-bottom & {\r\n      padding-left: 0;\r\n      padding-right: 0;\r\n    }\r\n  }\r\n\r\n  .nav.navbar-nav > li {\r\n    margin-left: 0;\r\n  }\r\n\r\n  .nav.navbar-nav > li > a {\r\n    padding: 16px 14px 14px 14px;\r\n  }\r\n\r\n  .nav.navbar-nav > li:first-child .dropdown-toggle {\r\n\r\n    .circle {\r\n      color: $gray;\r\n    }\r\n  }\r\n\r\n  .nav.navbar-nav .nav-item {\r\n    li:first-child .dropdown-item {\r\n      padding-top: 10px;\r\n    }\r\n\r\n    li:last-child .dropdown-item {\r\n      padding-bottom: 10px;\r\n    }\r\n  }\r\n\r\n  .nav.navbar-nav li:nth-child(2) > a:after {\r\n    content: none;\r\n  }\r\n\r\n  .nav.navbar-nav a[data-toggle=\"chat-sidebar\"] {\r\n    position: relative;\r\n  }\r\n\r\n  .navbar-nav {\r\n\r\n    .dropdown-menu {\r\n      margin-top: 0px;\r\n      padding: 0;\r\n    }\r\n  }\r\n}\r\n\r\n.navbar-nav .nav-item .nav-link {\r\n    margin-left: 0;\r\n}\r\n\r\n//\r\n// Breadcrumbs\r\n// --------------------------------------------------\r\n\r\n\r\n.breadcrumb {\r\n  color: $gray-light;\r\n  margin-bottom: $spacer-y*1.5;\r\n\r\n  > .active {\r\n    font-weight: $font-weight-semi-bold;\r\n  }\r\n\r\n  .content &{\r\n    padding-top: 0;\r\n    padding-bottom: 0;\r\n  }\r\n}\r\n\r\n.breadcrumb>li+li::before {\r\n  content: \">\";\r\n}\r\n//\r\n// Buttons\r\n// --------------------------------------------------\r\n\r\n.btn {\r\n  .glyphicon {\r\n    top: 3px;\r\n  }\r\n}\r\n\r\n.btn, .btn-inverse, .btn-gray {\r\n  &:not(.active) {\r\n    box-shadow: none !important;\r\n  }\r\n}\r\n\r\n.btn-sm {\r\n  padding-top: .35rem;\r\n  padding-bottom: .35rem;\r\n}\r\n\r\n.btn-rounded{\r\n  @include border-radius(6px);\r\n}\r\n\r\n.btn-gray{\r\n  @include button-variant($text-color, $gray-semi-lighter, darken($gray-semi-lighter, 5%));\r\n}\r\n\r\n.btn-outline{\r\n  @include button-variant($white, transparent, $white);\r\n\r\n  &:hover,\r\n  &:focus {\r\n    background-color: rgba($white, .1);\r\n  }\r\n}\r\n\r\n.btn-link:focus,\r\n.btn-link:active:focus,\r\n.btn-link.active:focus{\r\n  outline: 0;\r\n}\r\n\r\n.input-group > .input-group-btn:last-child > .btn[data-toggle=\"dropdown\"] {\r\n  @include border-right-radius($btn-border-radius);\r\n}\r\n\r\n\r\n.btn-group > .btn[data-toggle=\"dropdown\"][data-original-title][title] {\r\n  @include border-right-radius($btn-border-radius);\r\n\r\n  &:focus,\r\n  &:active{\r\n    outline: none !important;\r\n  }\r\n}\r\n\r\n\r\n//\r\n// Labels\r\n// --------------------------------------------------\r\n.label{\r\n  font-size: 11px;\r\n  font-weight: $font-weight-semi-bold;\r\n  padding: 6px;\r\n\r\n}\r\n\r\n\r\n//\r\n// Button groups\r\n// --------------------------------------------------\r\n\r\n.btn-toolbar{\r\n  margin-top: $line-height-computed/2;\r\n  margin-bottom: $line-height-computed/2;\r\n}\r\n\r\n//\r\n// Progress bars\r\n// --------------------------------------------------\r\n\r\n.js-progress-animate {\r\n  transition: width 0.6s ease;\r\n}\r\n\r\n.progress{\r\n  @include box-shadow(none);\r\n  transition: width 0.6s ease;\r\n  height: 1.3rem;\r\n}\r\n\r\n.progress-bar{\r\n  border-radius: $border-radius;\r\n  @include box-shadow(none);\r\n}\r\n\r\n.progress-sm{\r\n  height: 10px;\r\n  margin-bottom: $line-height-computed/2;\r\n}\r\n\r\n.progress-xs{\r\n  height: 5px;\r\n  margin-bottom: $line-height-computed/2;\r\n}\r\n\r\n\r\n.progress-bar-gray {\r\n  @include progress-variant($gray);\r\n}\r\n\r\n.progress-bar-gray-light {\r\n  @include progress-variant($gray-light);\r\n}\r\n\r\n.progress-primary {\r\n  @include progress-variant($brand-primary)\r\n}\r\n\r\n\r\n//\r\n// List groups\r\n// --------------------------------------------------\r\n\r\n.card {\r\n  margin-bottom: 0;\r\n  border: none;\r\n}\r\n\r\n.card-footer {\r\n  border-top: none;\r\n}\r\n\r\n.card > .list-group > .list-group-item {\r\n  border-width: 1px 0;\r\n\r\n  &:first-child {\r\n    border-radius: 0;\r\n  }\r\n}\r\n\r\n.list-group{\r\n  .widget-body.no-padding > &{\r\n    margin: 0;\r\n\r\n    .list-group-item{\r\n      border-width: 1px 0;\r\n    }\r\n  }\r\n}\r\n\r\n.list-group-item{\r\n\r\n  .list-group-lg & {\r\n    padding: 15px 15px;\r\n  }\r\n\r\n  .list-group:last-of-type &:last-child{\r\n    border-bottom: 0;\r\n  }\r\n}\r\n\r\n\r\n//\r\n// Tooltips\r\n// --------------------------------------------------\r\n\r\n.tooltip-inner{\r\n  @include border-radius(3px);\r\n}\r\n//\r\n// Alerts\r\n// --------------------------------------------------\r\n\r\n\r\n.alert-sm{\r\n  padding: 10px 12px;\r\n  font-size: $font-size-mini;\r\n\r\n  .close{\r\n    font-size: 18px;\r\n  }\r\n}\r\n\r\n.alert-dismissable .close{\r\n  position: static;\r\n  color: $close-color;\r\n}\r\n\r\n\r\n//\r\n// Popovers\r\n\r\n.popover{\r\n  border: none;\r\n  @include box-shadow(none);\r\n}\r\n\r\n.popover-content {\r\n  padding: 20px;\r\n}\r\n\r\n//\r\n// Modals\r\n// --------------------------------------------------\r\n\r\n// z-index fix. modal doesn't show up properly when .content-wrap is relative\r\n.modal-open{\r\n  &, &.nav-collapsed, &.nav-static{\r\n    .content-wrap{\r\n      -webkit-transform: none;\r\n      -ms-transform: none;\r\n      transform: none;\r\n      position: static;\r\n    }\r\n\r\n    .sidebar{\r\n      z-index: -1;\r\n    }\r\n  }\r\n}\r\n\r\n//\r\n// Pagination\r\n// --------------------------------------------------\r\n\r\n.pagination{\r\n  font-weight: $font-weight-normal;\r\n\r\n  > .page-item {\r\n    display: inline; // Remove list-style and block-level defaults\r\n    > .page-link,\r\n    > span{\r\n      border-radius: $border-radius;\r\n      margin: 0 2px;\r\n    }\r\n\r\n    >.page-link {\r\n      text-decoration: none;\r\n      border: none;\r\n    }\r\n  }\r\n}\r\n\r\n//\r\n// Jumbotron\r\n// --------------------------------------------------\r\n\r\n.jumbotron {\r\n  @include media-breakpoint-up(md){\r\n    padding-left: $jumbotron-padding;\r\n    padding-right: $jumbotron-padding;\r\n  }\r\n}\r\n\r\n\r\n// Navbar form\r\n//\r\n// Extension of the `.form-inline` with some extra flavor for optimum display in\r\n// our navbars.\r\n\r\n.navbar-form {\r\n  padding: 7px 0 6.5px $navbar-padding-horizontal;\r\n\r\n  .form-group {\r\n    margin-bottom: 0;\r\n  }\r\n\r\n  // Undo 100% width for pull classes\r\n  @media (min-width: map_get($grid-breakpoints, md)) {\r\n    width: auto;\r\n    border: 0;\r\n    margin-left: 0;\r\n    margin-right: 0;\r\n  }\r\n}\r\n\r\n//\r\n// Navbar Dasboard\r\n// --------------------------------------------------\r\n\r\n.navbar-dashboard {\r\n  background-color: $navbar-dashboard-bg;\r\n  border-color: $navbar-dashboard-border;\r\n\r\n  .navbar-brand {\r\n    color: $navbar-dashboard-brand-color;\r\n    margin-right: 0;\r\n    padding-top: 10px;\r\n    font-size: $font-size-lg;\r\n    &:hover,\r\n    &:focus {\r\n      color: $navbar-dashboard-brand-hover-color;\r\n      background-color: $navbar-dashboard-brand-hover-bg;\r\n    }\r\n  }\r\n\r\n  .navbar-text {\r\n    color: $navbar-dashboard-color;\r\n  }\r\n\r\n  .navbar-nav {\r\n    > li > a {\r\n      color: $navbar-dashboard-link-color;\r\n\r\n      &:hover,\r\n      &:focus {\r\n        color: $navbar-dashboard-link-hover-color;\r\n        background-color: $navbar-dashboard-link-hover-bg;\r\n      }\r\n    }\r\n    > .active > a {\r\n      &,\r\n      &:hover,\r\n      &:focus {\r\n        color: $navbar-dashboard-link-active-color;\r\n        background-color: $navbar-dashboard-link-active-bg;\r\n      }\r\n    }\r\n    > .disabled > a {\r\n      &,\r\n      &:hover,\r\n      &:focus {\r\n        color: $navbar-dashboard-link-disabled-color;\r\n        background-color: $navbar-dashboard-link-disabled-bg;\r\n      }\r\n    }\r\n  }\r\n\r\n  .navbar-nav {\r\n    // Remove background color from open dropdown\r\n    > .open > a {\r\n      &,\r\n      &:hover,\r\n      &:focus {\r\n        background-color: $navbar-dashboard-link-active-bg;\r\n        color: $navbar-dashboard-link-active-color;\r\n      }\r\n    }\r\n\r\n    @media (max-width: map_get($grid-breakpoints, md) - 1px) {\r\n      // Dropdowns get custom display when collapsed\r\n      .open .dropdown-menu {\r\n        > li > a {\r\n          color: $navbar-dashboard-link-color;\r\n          &:hover,\r\n          &:focus {\r\n            color: $navbar-dashboard-link-hover-color;\r\n            background-color: $navbar-dashboard-link-hover-bg;\r\n          }\r\n        }\r\n        > .active > a {\r\n          &,\r\n          &:hover,\r\n          &:focus {\r\n            color: $navbar-dashboard-link-active-color;\r\n            background-color: $navbar-dashboard-link-active-bg;\r\n          }\r\n        }\r\n        > .disabled > a {\r\n          &,\r\n          &:hover,\r\n          &:focus {\r\n            color: $navbar-dashboard-link-disabled-color;\r\n            background-color: $navbar-dashboard-link-disabled-bg;\r\n          }\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n  // Links in navbars\r\n  //\r\n  // Add a class to ensure links outside the navbar nav are colored correctly.\r\n\r\n  .btn-link {\r\n    color: $navbar-dashboard-link-color;\r\n    &:hover,\r\n    &:focus {\r\n      color: $navbar-dashboard-link-hover-color;\r\n    }\r\n    &[disabled],\r\n    fieldset[disabled] & {\r\n      &:hover,\r\n      &:focus {\r\n        color: $navbar-dashboard-link-disabled-color;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n\r\n@mixin bg-variant($parent, $color) {\r\n  #{$parent} {\r\n    background-color: $color !important;\r\n    color: $gray !important;\r\n  }\r\n  a#{$parent} {\r\n    @include hover-focus {\r\n      background-color: darken($color, 10%);\r\n    }\r\n  }\r\n}\r\n\r\n//\r\n// Labels\r\n// -----------------\r\n\r\n.label-pill {\r\n  color: $white;\r\n  font-weight: bold;\r\n  padding: .25em .6em;\r\n  font-size: 12px;\r\n}\r\n\r\n//\r\n// Code\r\n// -----------------\r\n\r\npre {\r\n  display: block;\r\n  padding: 6.5px;\r\n  margin: 0 0 ($line-height-computed / 2);\r\n  font-size: 13px;\r\n  line-height: $line-height;\r\n  word-break: break-all;\r\n  word-wrap: break-word;\r\n  color: $pre-color;\r\n  background-color: $pre-bg;\r\n  border: 1px solid $pre-border-color;\r\n  border-radius: $border-radius;\r\n\r\n  // Account for some code outputs that place code tags in pre tags\r\n  code {\r\n    padding: 0;\r\n    font-size: inherit;\r\n    color: inherit;\r\n    white-space: pre-wrap;\r\n    background-color: transparent;\r\n    border-radius: 0;\r\n  }\r\n}","@mixin nav-justified {\r\n  width: 100%;\r\n\r\n  .nav-item {\r\n    float: none;\r\n  }\r\n\r\n  .nav-link {\r\n    text-align: center;\r\n    margin-bottom: 5px;\r\n  }\r\n\r\n  > .dropdown .dropdown-menu { //todo: remove child selector\r\n    top: auto;\r\n    left: auto;\r\n  }\r\n\r\n  @include media-breakpoint-up(md) {\r\n    .nav-item {\r\n      display: table-cell;\r\n      width: 1%;\r\n    }\r\n    .nav-link {\r\n      margin-bottom: 0;\r\n    }\r\n  }\r\n}\r\n\r\n// Move borders to anchors instead of bottom of list\r\n//\r\n// Mixin for adding on top the shared `.nav-justified` styles for our tabs\r\n@mixin nav-tabs-justified {\r\n  border-bottom: 0;\r\n\r\n  .nav-link {\r\n    // Override margin from .nav-tabs\r\n    margin-right: 0;\r\n  }\r\n}\r\n\r\n@mixin navbar-vertical-align($element-height) {\r\n  margin-top: (($navbar-height - $element-height) / 2);\r\n  margin-bottom: (($navbar-height - $element-height) / 2);\r\n}\r\n\r\n@mixin progress-bar-variant($color) {\r\n  background-color: $color;\r\n\r\n  // Deprecated parent class requirement as of v3.2.0\r\n  .progress-striped & {\r\n    @include gradient-striped;\r\n  }\r\n}\r\n\r\n// Classes to be applied for high ppi displays\r\n// -------------------------\r\n@mixin retina(){\r\n  @media (min--moz-device-pixel-ratio: 1.3),\r\n           (-o-min-device-pixel-ratio: 2.6/2),\r\n      (-webkit-min-device-pixel-ratio: 1.3),\r\n              (min-device-pixel-ratio: 1.3),\r\n                  (min-resolution: 1.3dppx) {\r\n    @content;\r\n  }\r\n}\r\n\r\n// Common thumbnail properties\r\n// -------------------------\r\n@mixin thumb($parent, $size){\r\n  #{$parent} {\r\n    width: $size;\r\n\r\n    > .glyphicon,\r\n    > .fa{\r\n      line-height: $size;\r\n    }\r\n  }\r\n}\r\n\r\n@mixin border-radius($radius) {\r\n  border-radius: $radius;\r\n}\r\n\r\n\r\n// Generates single property classes\r\n// -------------------------\r\n@mixin property-variants($base, $property, $variants: ()){\r\n  @each $size, $value in $variants {\r\n    @if $size == 'md'{\r\n      #{$base} {\r\n        #{$property}: $value;\r\n      }\r\n      #{$base}-n {\r\n        #{$property}: $value;\r\n      }\r\n    }\r\n    #{$base}-#{$size} {\r\n      #{$property}: $value;\r\n    }\r\n    #{$base}-n-#{$size} {\r\n      #{$property}: -$value;\r\n    }\r\n  }\r\n}\r\n\r\n@mixin keyframes($name){\r\n  @-webkit-keyframes #{$name} {\r\n    @content;\r\n  }\r\n\r\n  @keyframes #{$name} {\r\n    @content;\r\n  }\r\n}\r\n\r\n@mixin panel-variant($border, $heading-text-color, $heading-bg-color, $heading-border) {\r\n  border-color: $border;\r\n\r\n  & > .panel-heading {\r\n    color: $heading-text-color;\r\n    background-color: $heading-bg-color;\r\n    border-color: $heading-border;\r\n\r\n    + .panel-collapse > .panel-body {\r\n      border-top-color: $border;\r\n    }\r\n  }\r\n  & > .panel-footer {\r\n    + .panel-collapse > .panel-body {\r\n      border-bottom-color: $border;\r\n    }\r\n  }\r\n}","/**\r\n * Animate.css\r\n */\r\n\r\n.animated{\r\n  animation-duration: .5s;\r\n}\r\n\r\n/**\r\n * Pace\r\n */\r\n.pace .pace-progress{\r\n  background: $brand-primary;\r\n  position: absolute;\r\n  top: $navbar-height;\r\n}\r\n\r\n.pace .pace-activity{\r\n  position: absolute;\r\n  top: 65px;\r\n  border-width: 1px;\r\n  left: 15px;\r\n  right: auto;\r\n  width: 18px;\r\n  height: 18px;\r\n  border-top-color: $brand-primary;\r\n  border-left-color: $brand-primary;\r\n}\r\n\r\n\r\n\r\n.pace .pace-progress-inner{\r\n  right: -10px;\r\n  width: 10px;\r\n}\r\n/**\r\n * No More Tables\r\n * http://css-tricks.com/responsive-data-tables/\r\n * Just mark your cells with col header attribute <td data-col-title=\"Surname\">Otto</td>\r\n */\r\n\r\n.table-no-more{\r\n  @include media-breakpoint-down(sm) {\r\n    /* Force table to not be like tables anymore */\r\n    &, thead, tbody, th, td, tr {\r\n      display: block;\r\n    }\r\n\r\n    /* Hide table headers (but not display: none;, for accessibility) */\r\n    thead > tr {\r\n      position: absolute;\r\n      top: -9999px;\r\n      left: -9999px;\r\n    }\r\n\r\n    tbody > tr > td {\r\n      /* Behave  like a \"row\" */\r\n      border: none;\r\n      border-bottom: 1px solid #eee;\r\n      position: relative;\r\n      padding-left: 50%;\r\n    }\r\n\r\n    tbody > tr > td:before {\r\n      /* Now like a table header */\r\n      position: absolute;\r\n      /* Top/left values mimic padding */\r\n      top: 6px;\r\n      left: 6px;\r\n      width: 45%;\r\n      padding-right: 10px;\r\n      white-space: nowrap;\r\n      content: attr(data-col-title);\r\n    }\r\n  }\r\n}\r\n\r\n/**\r\n * Wysihtml\r\n */\r\n\r\n.wysihtml5-toolbar {\r\n  .dropdown-menu > li > a {\r\n    display: block;\r\n    width: 100%; // For `<button>`s\r\n    padding: 3px 20px;\r\n    clear: both;\r\n    font-weight: normal;\r\n    line-height: $line-height;\r\n    color: $dropdown-link-color;\r\n    text-align: inherit; // For `<button>`s\r\n    white-space: nowrap; // prevent links from randomly breaking onto new lines\r\n    backg