rgin: 0 0 calc(#{$line-height-computed} + 6px);\r\n}\r\n\r\n.notifications{\r\n  @include media-breakpoint-up(lg){\r\n    width: 333px;\r\n  }\r\n\r\n  .card-header {\r\n    .btn-group-justified {\r\n      display: flex;\r\n\r\n      label {\r\n        flex: 1;\r\n        padding-top: 5px;\r\n        padding-bottom: 5px;\r\n      }\r\n    }\r\n\r\n  }\r\n\r\n  height: 100%;\r\n\r\n  .list-group{\r\n    height: 320px;\r\n    overflow-y: scroll;\r\n  }\r\n  .list-group-item{\r\n    @include transition(background-color .15s ease-in-out);\r\n    &:hover{\r\n      background-color: $list-group-hover-bg;\r\n      .progress-bar{\r\n        background-color: $white !important;\r\n      }\r\n    }\r\n    .progress{\r\n      @include transition(background-color .15s ease-in-out);\r\n    }\r\n  }\r\n\r\n  .btn-notifications-reload{\r\n    color: $navbar-dashboard-link-color;\r\n  }\r\n}\r\n\r\n/***********************/\r\n/*        Loader       */\r\n/***********************/\r\n\r\n.loader-wrap{\r\n  position: fixed;\r\n  z-index: 0;\r\n  right: 0;\r\n  top: 0;\r\n  bottom: 0;\r\n  text-align: center;\r\n  background-color: $body-bg;\r\n\r\n  opacity: 1;\r\n  transition: transform $sidebar-transition-time ease-in-out, left $sidebar-transition-time ease-in-out, opacity .2s ease-out, background-color .2s ease-out;\r\n  &.hiding{\r\n    opacity: 0;\r\n    background-color: rgba(0,0,0,0);\r\n  }\r\n\r\n  .fa{\r\n    position: absolute;\r\n    top: 50%;\r\n    left: 50%;\r\n    line-height: 30px;\r\n    margin-top: -15px;\r\n    margin-left: -10px;\r\n    font-size: 20px;\r\n    vertical-align: middle;\r\n  }\r\n\r\n  .nav-static & {\r\n    @include media-breakpoint-up(lg){\r\n      @include transition(opacity .2s ease-out, background-color .2s ease-out);\r\n      transform: translate(0, 0);\r\n      margin-left: $sidebar-width;\r\n    }\r\n  }\r\n}\r\n\r\n/***********************/\r\n/*       Widget        */\r\n/***********************/\r\n\r\n.widget{\r\n  position: relative;\r\n  margin-bottom: $grid-gutter-width;\r\n  padding: $widget-padding-vertical $widget-padding-horizontal;\r\n  background: $widget-bg-color;\r\n  border-radius: $border-radius-sm;\r\n  > header{\r\n    margin: (-$widget-padding-vertical) (-$widget-padding-horizontal);\r\n    padding: $widget-padding-vertical $widget-padding-horizontal;\r\n    h1,h2,h3,h4,h5,h6{\r\n      margin: 0;\r\n    }\r\n\r\n    + .widget-body,\r\n    + .widget-body.no-padding{\r\n      margin-top: $widget-padding-vertical;\r\n    }\r\n  }\r\n\r\n  > .widget-body.no-padding{\r\n    margin: (-$widget-padding-vertical) (-$widget-padding-horizontal);\r\n\r\n    + footer{\r\n      margin-top: $widget-padding-vertical;\r\n    }\r\n  }\r\n\r\n  > footer{\r\n    margin: 0 (-$widget-padding-horizontal) (-$widget-padding-vertical);\r\n    padding: $widget-padding-vertical $widget-padding-horizontal;\r\n  }\r\n\r\n  &:hover .widget-controls-hover{\r\n    opacity: 1;\r\n  }\r\n\r\n  .loader{\r\n    position: absolute;\r\n    top: 0;\r\n    bottom: 0;\r\n    left: 0;\r\n    right: 0;\r\n\r\n    .spinner{\r\n      position: absolute;\r\n      top: 50%;\r\n      width: 100%; //ie fix\r\n      margin-top: -10px;\r\n      font-size: 20px;\r\n      text-align: center;\r\n    }\r\n  }\r\n\r\n  .widget-table-overflow{\r\n    margin: 0 (-$widget-padding-horizontal) (-$widget-padding-vertical);\r\n  }\r\n}\r\n\r\n.widget-controls{\r\n  position: absolute;\r\n  z-index: 1;\r\n  top: 0;\r\n  right: 0;\r\n  padding: 14px;\r\n  font-size: $font-size-sm;\r\n\r\n  &.left{\r\n    left: 0;\r\n    right: auto;\r\n  }\r\n\r\n  &.widget-controls-hover{\r\n    opacity: 0;\r\n    @include transition(opacity .15s);\r\n  }\r\n\r\n\r\n\r\n  > a{\r\n    padding: 1px 4px;\r\n    border-radius: 4px;\r\n    color: rgba($black, .4);\r\n    @include transition(color .15s ease-in-out);\r\n\r\n    &:hover{\r\n      color: rgba($black, .1);\r\n      text-decoration: none;\r\n    }\r\n\r\n    > .glyphicon{\r\n      vertical-align: 0;\r\n      font-size: 12px;\r\n    }\r\n\r\n    > .fa{\r\n      vertical-align: 1px;\r\n    }\r\n  }\r\n\r\n  > .btn-group{\r\n    vertical-align: baseline;\r\n  }\r\n}\r\n\r\n.widget-card { min-height: 135px;}\r\n\r\n.widget-icon{\r\n  opacity: .5;\r\n  font-size: 42px;\r\n  height: 70px;\r\n\r\n  .glyphicon {\r\n    top: 5px;\r\n  }\r\n}\r\n\r\n.widget-image{\r\n  position: relative;\r\n  overflow: hidden;\r\n  margin: (-$widget-padding-vertical) (-$widget-padding-horizontal);\r\n  border-radius: $border-radius;\r\n\r\n  > img{\r\n    max-width: 100%;\r\n    border-radius: $border-radius $border-radius 0 0;\r\n    transition: transform .15s ease;\r\n  }\r\n\r\n  &:hover > img{\r\n    transform: scale(1.1, 1.1);\r\n  }\r\n\r\n  .title{\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    margin: 20px;\r\n  }\r\n\r\n  .info{\r\n    position: absolute;\r\n    top: 0;\r\n    right: 0;\r\n    margin: 20px;\r\n  }\r\n}\r\n\r\n.widget-footer-bottom{\r\n  position: absolute;\r\n  bottom: 0;\r\n  width: 100%;\r\n}\r\n\r\n.widget-sm{\r\n  height: 230px;\r\n}\r\n\r\n.widget-md{\r\n  height: 373px;\r\n}\r\n\r\n.windget-padding-md {\r\n  padding: $widget-padding-vertical $widget-padding-horizontal;\r\n}\r\n\r\n.windget-padding-lg {\r\n  padding: $widget-padding-vertical*2 $widget-padding-horizontal*2;\r\n}\r\n\r\n.widget-body-container{\r\n  position: relative;\r\n  height: 100%;\r\n}\r\n\r\n.widget-top-overflow,\r\n.widget-middle-overflow{\r\n  position: relative;\r\n  margin: 0 (-$widget-padding-horizontal);\r\n\r\n  > img{\r\n    max-width: 100%;\r\n  }\r\n}\r\n\r\n.widget-top-overflow{\r\n  margin-top: (-$widget-padding-vertical);\r\n  border-top-left-radius: $border-radius;\r\n  border-top-right-radius: $border-radius;\r\n  overflow: hidden;\r\n\r\n  > img{\r\n    border-top-left-radius: $border-radius;\r\n    border-top-right-radius: $border-radius;\r\n  }\r\n\r\n  > .btn-toolbar {\r\n    position: absolute;\r\n    top: 0;\r\n    right: 0;\r\n    z-index: 1;\r\n    margin-right: $widget-padding-horizontal;\r\n\r\n    @include media-breakpoint-up(md){\r\n      top: auto;\r\n      bottom: 0;\r\n    }\r\n  }\r\n}\r\n\r\n/***********************************/\r\n/**         Widgets Grid          **/\r\n/***********************************/\r\n\r\n.widget-container{\r\n  min-height: 30px;\r\n\r\n  .widget:not(.fullscreened) > header{\r\n    cursor: move;\r\n  }\r\n}\r\n\r\n.widget-placeholder{\r\n  background:  rgba($white, .4);\r\n  border: 1px dashed $gray-light;\r\n  margin: -1px -1px calc(#{$grid-gutter-width} - 1px) -1px;\r\n}\r\n\r\n/***********************/\r\n/*        Forms        */\r\n/***********************/\r\n\r\n.form-actions{\r\n  margin: $line-height-computed 0 0;\r\n  padding: $line-height-computed;\r\n  background-color: #f5f5f5;\r\n\r\n  .form-horizontal & .row{\r\n    margin-left: ($grid-gutter-width/-2) - $line-height-computed;\r\n    margin-right: ($grid-gutter-width/-2) - $line-height-computed;\r\n  }\r\n}\r\n\r\ninput[type=search], input.search {\r\n  &, .input-group-lg > &, .input-group-sm > &{\r\n    padding-left: 26px;\r\n  }\r\n  background-color: $input-bg;\r\n  background:url(../img/search.png) 5px 60% no-repeat $input-bg;\r\n}\r\n\r\n/***********************/\r\n/*       Buttons       */\r\n/***********************/\r\n\r\n.btn-inverse {\r\n  @include button-variant($white, $gray, darken($gray, 10%));\r\n}\r\n\r\n.btn-xs {\r\n  padding: 1px 5px;\r\n  font-size: 12px;\r\n  line-height: 1.5;\r\n  border-radius: 3px;\r\n}\r\n\r\n/***********************/\r\n/*     Content Map     */\r\n/***********************/\r\n\r\n.content-map{\r\n  position: absolute;\r\n  top: 0;\r\n  bottom: 0;\r\n  left: 0;\r\n  right: 0;\r\n  + .page-title{\r\n    position: relative;\r\n    z-index: 2;\r\n  }\r\n}\r\n\r\n.content-map-controls{\r\n  position: absolute;\r\n  right: 0;\r\n  top: 0;\r\n  z-index: 2;\r\n  margin-top: $content-padding-top;\r\n  margin-right: $content-padding-horizontal;\r\n}","/***********************/\r\n/*                     */\r\n/*   Custom Widgets    */\r\n/*                     */\r\n/***********************/\r\n\r\n\r\n/***********************/\r\n/*     Chart Simple    */\r\n/***********************/\r\n\r\n.widget-chart-simple{\r\n\r\n  .widget-body{\r\n    padding-top: 10px;\r\n  }\r\n  .chart-value{\r\n    font-size: 24px;\r\n  }\r\n\r\n  .chart{\r\n    margin-top: 20px;\r\n  }\r\n}\r\n\r\n.jqstooltip{\r\n  box-sizing: content-box;\r\n}\r\n\r\n/***********************/\r\n/*    Chart Changes    */\r\n/***********************/\r\n\r\n.widget-chart-changes{\r\n  .chart{\r\n    margin: -$widget-padding-vertical (-$widget-padding-horizontal) 0;\r\n    padding: $widget-padding-vertical 0 0;\r\n  }\r\n\r\n  .chart-title{\r\n    margin: 20px 0 0;\r\n  }\r\n\r\n  .chart-value, .chart-value-change{\r\n    padding: 0 $widget-padding-horizontal;\r\n  }\r\n\r\n  .chart-value{\r\n    margin-bottom: 0;\r\n    font-weight: $font-weight-bold;\r\n    font-size: 21px;\r\n    line-height: 1;\r\n    color: $white;\r\n  }\r\n\r\n  .chart-value-change{\r\n    color: rgba($white, .7);\r\n    font-size: $font-size-smaller;\r\n    margin-bottom: $chart-spacer-y;\r\n  }\r\n}\r\n\r\n/***********************/\r\n/*  Chart Stats Simple */\r\n/***********************/\r\n\r\n.widget-chart-stats-simple{\r\n  > header{\r\n    h1,h2,h3,h4,h5,h6{\r\n      margin-bottom: $line-height-computed/4;\r\n    }\r\n  }\r\n\r\n  .chart{\r\n    height: 200px;\r\n    margin: $line-height-computed/2 (-$widget-padding-horizontal) (-$widget-padding-vertical);\r\n    > .chart-inner{\r\n      height: 100%;\r\n    }\r\n  }\r\n}\r\n\r\n/***********************/\r\n/*      Big Chart      */\r\n/***********************/\r\n\r\n.widget-big-chart{\r\n  .chart{\r\n    height: 200px;\r\n  }\r\n}\r\n\r\n/***********************/\r\n/*  Part:Chart Stats   */\r\n/***********************/\r\n\r\n.chart-stats{\r\n  border-top: 1px solid $gray-lighter;\r\n}\r\n\r\n/***********************/\r\n/* Part:Progress Stats */\r\n/***********************/\r\n\r\n.progress-stats{\r\n  .name{\r\n    margin-bottom: 2px;\r\n  }\r\n  .description{\r\n    margin-bottom: 2px;\r\n  }\r\n  .status{\r\n    margin-top: 15px;\r\n  }\r\n\r\n  @include media-breakpoint-down(lg) {\r\n    .description + .progress{\r\n      margin-bottom: 0;\r\n    }\r\n  }\r\n}\r\n\r\n/***********************/\r\n/*    Part:Stats Row   */\r\n/***********************/\r\n\r\n.stats-row{\r\n  margin-bottom: 15px;\r\n}\r\n\r\n.stat-item{\r\n  display: inline-block;\r\n  padding-right: 15px;\r\n\r\n  & + .stat-item{\r\n    padding-left: 15px;\r\n    border-left: 1px solid $hr-border-color;\r\n  }\r\n  .name{\r\n    margin-bottom: 2px;\r\n    margin-top: 10px;\r\n  }\r\n  .value{\r\n    font-weight: bold;\r\n  }\r\n\r\n  &.stat-item-mini-chart{\r\n    position: relative;\r\n    top: -12px;\r\n    padding-left: 0;\r\n    border-left: none;\r\n  }\r\n}\r\n\r\n\r\n/***********************/\r\n/*  Part:Map Controls  */\r\n/***********************/\r\n\r\n.map-controls{\r\n  position: absolute;\r\n  bottom: 0;\r\n  left: 0;\r\n  right: 0;\r\n  border-top: 1px solid #bbb;\r\n  background-color: $gray-semi-lighter;\r\n  border-bottom-left-radius: $border-radius;\r\n  border-bottom-right-radius: $border-radius;\r\n\r\n  > .nav-item > .nav-link{\r\n    border-radius: 0;\r\n    padding: 0.7143rem 0 0.7143rem 0;\r\n\r\n    color: $gray;\r\n    &:hover{\r\n      background-color: $gray-lighter;\r\n      color: $gray\r\n    }\r\n  }\r\n\r\n  > .nav-item.active > .nav-link{\r\n    &, &:hover{\r\n      background-color: $white;\r\n      color: $gray;\r\n      font-weight: $font-weight-bold;\r\n    }\r\n  }\r\n\r\n  > .nav-item:first-child > a{\r\n    border-bottom-left-radius: $border-radius;\r\n  }\r\n\r\n  > .nav-item:last-child > a{\r\n    border-bottom-right-radius: $border-radius;\r\n  }\r\n}\r\n\r\n.map svg {\r\n  height: 100%;\r\n}\r\n\r\n/***********************************/\r\n/**          NEWS LIST           **/\r\n/**********************************/\r\n\r\n.news-list{\r\n  margin-bottom: 0;\r\n  padding-left: 0;\r\n\r\n  li{\r\n    list-style: none;\r\n    box-sizing: content-box;\r\n    border-top: 1px solid $gray-lighter;\r\n    padding: 12px;\r\n    cursor: pointer;\r\n    @include transition(background-color .2s ease-out);\r\n\r\n    &:hover{\r\n      background: $body-bg-light;\r\n    }\r\n\r\n    &:last-child{\r\n      margin-bottom: -10px;\r\n    }\r\n  }\r\n\r\n  img,\r\n  .icon{\r\n    float: left;\r\n    height: 50px;\r\n    width: 50px;\r\n  }\r\n\r\n  .icon{\r\n    line-height: 50px;\r\n    border-radius: 50%;\r\n    text-align: center;\r\n    font-size: 28px;\r\n  }\r\n\r\n  .news-item-info{\r\n    margin-left: 62px; /*50 + 12px padding*/\r\n  }\r\n\r\n  .name{\r\n    text-transform: uppercase;\r\n\r\n    a{\r\n      text-decoration: none;\r\n      &:hover{\r\n        color: $link-color;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n\r\n/***********************************/\r\n/**      LIST GROUP SORTABLE      **/\r\n/***********************************/\r\n\r\n.list-group-sortable{\r\n  > .list-group-item{\r\n    margin-bottom: 0;\r\n    border-radius: $border-radius;\r\n\r\n    + .list-group-item{\r\n      margin-top: $line-height-computed/2;\r\n    }\r\n  }\r\n\r\n  > .list-group-item-placeholder{\r\n    border: 1px dashed $gray-light;\r\n    background-color: $gray-semi-lighter;\r\n  }\r\n\r\n  &:last-of-type > .list-group-item:last-child{\r\n    border-bottom: 1px solid $list-group-border-color;\r\n  }\r\n}\r\n\r\n/***********************************/\r\n/**           ICON LIST           **/\r\n/***********************************/\r\n\r\n.icon-list{\r\n  margin-top: $line-height-computed;\r\n}\r\n\r\n.icon-list-item{\r\n  height: 32px;\r\n  font-size: 14px;\r\n  line-height: 32px;\r\n  > a{\r\n    color: $text-color;\r\n    text-decoration: none;\r\n  }\r\n\r\n\r\n  .glyphicon,\r\n  .fa{\r\n    width: 32px;\r\n    margin-right: 10px;\r\n  }\r\n\r\n  .glyphicon {\r\n    top: 10px;\r\n  }\r\n\r\n  &:hover{\r\n    .glyphicon,\r\n    .fa{\r\n      font-size: 28px;\r\n      top: 2px;\r\n    }\r\n\r\n    .fa{\r\n      vertical-align: -5px;\r\n    }\r\n\r\n    .glyphicon{\r\n      vertical-align: -6px;\r\n    }\r\n  }\r\n}\r\n\r\n/***********************************/\r\n/**            INVOICE            **/\r\n/***********************************/\r\n\r\n.widget-invoice{\r\n  padding: 40px;\r\n\r\n  .print-buttons {\r\n    .btn {\r\n      @include media-breakpoint-down(xs) {\r\n        padding-left: 0.8rem;\r\n        padding-right: 0.8rem;\r\n      }\r\n    }\r\n  }\r\n\r\n  > header{\r\n    .invoice-logo{\r\n      max-height: 50px;\r\n    }\r\n\r\n    + .widget-body{\r\n      margin-top: 70px;\r\n    }\r\n  }\r\n}\r\n\r\n/***********************************/\r\n/**             LOGIN             **/\r\n/***********************************/\r\n\r\n.login-page{\r\n  background-color: $gray-semi-lighter;\r\n}\r\n\r\n.login-page .page-footer {\r\n  margin-bottom: 30px;\r\n  font-size: $font-size-mini;\r\n  color: $text-muted;\r\n  text-align: center;\r\n  @media (min-height: 600px) {\r\n    position: absolute;\r\n    bottom: 0;\r\n    left: 0;\r\n    right: 0;\r\n  }\r\n}\r\n\r\n.widget-login-container{\r\n  padding-top: 10%;\r\n}\r\n\r\n.widget-login-logo{\r\n  margin-top: 15px;\r\n  margin-bottom: 15px;\r\n  text-align: center;\r\n  font-weight: $font-weight-normal;\r\n  .fa-circle{\r\n    font-size: 13px;\r\n    margin: 0 20px;\r\n  }\r\n}\r\n\r\n.widget-login{\r\n  padding: 30px;\r\n\r\n  > header{\r\n    h1,h2,h3,h4,h5,h6{\r\n      font-weight: $font-weight-normal;\r\n    }\r\n  }\r\n}\r\n\r\n.widget-login-info{\r\n  font-size: $font-size-mini;\r\n  color: #888;\r\n  margin-top: 1px;\r\n  margin-bottom: 0;\r\n}\r\n\r\n.login-form{\r\n  .form-control{\r\n    font-size: $font-size-mini;\r\n    border: none;\r\n    background-color: $gray-lighter;\r\n    &:focus{\r\n      background-color: $gray-semi-lighter;\r\n    }\r\n  }\r\n}\r\n\r\n/***********************************/\r\n/**        Full Calendar          **/\r\n/***********************************/\r\n\r\n.full-calendar{\r\n  margin-top: 10px;\r\n}\r\n\r\n.calendar-controls{\r\n  .btn{\r\n    font-size: $font-size-mini;\r\n  }\r\n}\r\n\r\n.calendar-external-events{\r\n  margin-top: 20px;\r\n\r\n  .external-event{\r\n    margin: 10px 0;\r\n    padding: 6px;\r\n    font-size: $font-size-mini;\r\n    cursor: pointer;\r\n    border-radius: $border-radius;\r\n  }\r\n}\r\n\r\n.widget-calendar{\r\n  @include media-breakpoint-up(xl){\r\n    margin-top: -100px;\r\n  }\r\n}\r\n\r\n/***********************************/\r\n/**          Post Links           **/\r\n/***********************************/\r\n\r\n.post-links{\r\n  margin-bottom: 0;\r\n  font-size: $font-size-sm;\r\n  padding-left: 0;\r\n  @include clearfix();\r\n  > li{\r\n    float: left;\r\n    list-style: none;\r\n\r\n    + li{\r\n      &:before{\r\n        color: #999;\r\n        content: \"\\25cf\";\r\n        padding: 0 8px;\r\n      }\r\n    }\r\n    > a{\r\n      text-decoration: none;\r\n      color: $text-muted;\r\n      &:hover{\r\n        color: $text-muted;\r\n      }\r\n    }\r\n  }\r\n\r\n  &.no-separator > li + li{\r\n    margin-left: 12px;\r\n    &:before{\r\n      content: normal;\r\n    }\r\n  }\r\n}\r\n\r\n/***********************************/\r\n/**          Post Comments           **/\r\n/***********************************/\r\n\r\n.post-comments{\r\n  font-size: $font-size-sm;\r\n  padding-left: 0;\r\n  @include clearfix();\r\n\r\n  .post-links + &{\r\n    margin-top: $line-height-computed / 2;\r\n  }\r\n\r\n  > li {\r\n    padding: 10px;\r\n    border-top: 1px solid #e7e7e7;\r\n    list-style: none;\r\n    @include clearfix();\r\n\r\n    &:last-child{\r\n      padding-bottom: 0;\r\n    }\r\n  }\r\n\r\n  p:last-child{\r\n    margin-bottom: 0;\r\n  }\r\n\r\n  .avatar{\r\n    margin-top: 1px;\r\n  }\r\n\r\n  .author{\r\n    margin-top: 0;\r\n    margin-bottom: 2px;\r\n    colo