ign: center;\r\n\r\n    @include media-breakpoint-up(lg){\r\n      padding-right: 0;\r\n    }\r\n\r\n    .value {\r\n      font-size: 28px;\r\n      font-weight: $font-weight-base;\r\n    }\r\n  }\r\n}\r\n\r\n.activities{\r\n  h3 {\r\n    margin-left: 20px\r\n  }\r\n}\r\n.event{\r\n  .activities &{\r\n    margin-top: $line-height-computed;\r\n    width: 100%;\r\n  }\r\n}\r\n/***********************************/\r\n/**           Time Line           **/\r\n/***********************************/\r\n\r\n.timeline{\r\n  position: relative;\r\n  min-height: 100%;\r\n  list-style: none;\r\n  padding-left: 0;\r\n  margin-bottom: -40px; /* content padding bottom */\r\n  padding-bottom: 80px;\r\n\r\n  > li {\r\n    @include clearfix();\r\n\r\n    + li{\r\n      margin-top: 30px;\r\n    }\r\n  }\r\n\r\n  /* the time line :) */\r\n  &:before{\r\n    position: absolute;\r\n    top: 0;\r\n    bottom: 0;\r\n    left: 24%;\r\n    width: 8px;\r\n    content: \" \";\r\n    margin-left: -4px;\r\n    background-color: $white;\r\n\r\n    @include media-breakpoint-up(lg){\r\n      left: 50%;\r\n      margin-left: -4px;\r\n    }\r\n  }\r\n\r\n}\r\n\r\n.event{\r\n  background: $white;\r\n  border-radius: $border-radius;\r\n  padding: 20px 20px 0;\r\n  position: relative;\r\n\r\n  .timeline & {\r\n    float: right;\r\n    width: 68%;\r\n\r\n    &:before{\r\n      right: 100%;\r\n      content: \" \";\r\n      height: 0;\r\n      width: 0;\r\n      position: absolute;\r\n      border: 10px solid rgba(0,0,0,0);\r\n      border-right-color: $white;\r\n      top: 15px;\r\n    }\r\n  }\r\n\r\n  .post-comments{\r\n    margin-left: -20px;\r\n    margin-right: -20px;\r\n  }\r\n\r\n  > footer{\r\n    margin: 20px -20px 0;\r\n    padding: 10px 20px;\r\n    border-bottom-left-radius: $border-radius;\r\n    border-bottom-right-radius: $border-radius;\r\n    background-color: #f3f3f3;\r\n\r\n    @include clearfix();\r\n\r\n    .thumb{\r\n      margin-left: 10px;\r\n    }\r\n  }\r\n\r\n  @include media-breakpoint-up(lg){\r\n    .timeline & {\r\n      width: 45%;\r\n    }\r\n\r\n    .timeline > li.on-left &{\r\n      float: left;\r\n      &:before{\r\n        right: auto;\r\n        left: 100%;\r\n        border-right-color: rgba(0,0,0,0);\r\n        border-left-color: $white;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n.event-time{\r\n  .timeline &{\r\n    float: left;\r\n    width: 18%;\r\n    margin-top: 5px;\r\n    text-align: right;\r\n\r\n    > .date{\r\n      display: block;\r\n      font-size: $font-size-larger;\r\n    }\r\n\r\n    > .time{\r\n      display: block;\r\n      font-size: $font-size-lg;\r\n      font-weight: $font-weight-normal;\r\n    }\r\n  }\r\n\r\n  @include media-breakpoint-up(lg){\r\n    .timeline &{\r\n      width: 46%;\r\n    }\r\n\r\n    .timeline > li.on-left &{\r\n\r\n      float: right;\r\n      text-align: left;\r\n    }\r\n  }\r\n}\r\n\r\n.event-icon{\r\n  .timeline &{\r\n    position: absolute;\r\n    left: 24%;\r\n    width: 50px;\r\n    height: 50px;\r\n    line-height: 37px;\r\n    margin-left: -25px;\r\n    background-color: $white;\r\n    border: 7px solid $white;\r\n    border-radius: 50%;\r\n    text-align: center;\r\n\r\n    .glyphicon {\r\n      top: 11px;\r\n    }\r\n\r\n    &.event-icon-danger{ background-color: $brand-danger; border-color: lighten($brand-danger, 7%)}\r\n    &.event-icon-warning{ background-color: $brand-warning; border-color: lighten($brand-warning, 7%)}\r\n    &.event-icon-success{ background-color: $brand-success; border-color: lighten($brand-success, 7%)}\r\n    &.event-icon-info{ background-color: $brand-info; border-color: lighten($brand-info, 7%)}\r\n    &.event-icon-primary{ background-color: $brand-primary; border-color: lighten($brand-primary, 7%)}\r\n\r\n    &.event-icon-danger,\r\n    &.event-icon-warning,\r\n    &.event-icon-success,\r\n    &.event-icon-info,\r\n    &.event-icon-primary{\r\n      color: $white;\r\n    }\r\n\r\n    @include media-breakpoint-up(lg){\r\n      left: 50%;\r\n    }\r\n\r\n    > img{\r\n      width: 36px;\r\n      height: 36px;\r\n      margin-top: -4px;\r\n    }\r\n  }\r\n}\r\n\r\n.event-heading{\r\n  margin: 0 0 2px;\r\n  font-weight: $font-weight-semi-bold;\r\n  > a {\r\n    text-decoration: none;\r\n    color: #7ca9dd;\r\n  }\r\n  > small{\r\n    font-weight: $font-weight-semi-bold;\r\n    > a{\r\n      text-decoration: none;\r\n      color: $text-muted;\r\n    }\r\n  }\r\n}\r\n\r\n.event-map{\r\n  height: 200px;\r\n  margin: 0 -20px -20px;\r\n  overflow: visible !important;\r\n}\r\n\r\n.event-image{\r\n  margin: 0 -20px -20px;\r\n  max-height: 260px;\r\n  overflow: hidden;\r\n  > img{\r\n    max-width: 100%;\r\n  }\r\n}\r\n\r\n/***********************************/\r\n/**          ERROR PAGE           **/\r\n/***********************************/\r\n\r\n.error-page{\r\n  background-color: $gray-semi-lighter;\r\n}\r\n\r\n.error-container{\r\n  padding-top: 5%;\r\n  text-align: center;\r\n\r\n  > .btn{\r\n    padding-left: 35px;\r\n    padding-right: 35px;\r\n  }\r\n}\r\n\r\n.error-code{\r\n  margin: 20px;\r\n  font-size: 80px;\r\n  font-weight: $font-weight-normal;\r\n  color: $gray-dark;\r\n  @include media-breakpoint-up(md){\r\n    font-size: 180px;\r\n  }\r\n}\r\n\r\n.error-info{\r\n  font-size: 20px;\r\n  color: $gray-dark;\r\n}\r\n\r\n.error-help{\r\n  font-size: 14px;\r\n}\r\n\r\n.error-page .page-footer {\r\n  position: absolute;\r\n  bottom: 30px;\r\n  left: 0;\r\n  right: 0;\r\n  width: 100%;\r\n  font-size: $font-size-mini;\r\n  color: $text-muted;\r\n  text-align: center;\r\n}\r\n\r\n/***********************************/\r\n/**             EMAIL             **/\r\n/***********************************/\r\n\r\n\r\n.nav-email-folders{\r\n  > li > .nav-link {\r\n    color: $gray-semi-light;\r\n    font-weight: $font-weight-normal;\r\n    &:hover{\r\n      background-color: #e5e5e5;\r\n      color: $text-color;\r\n    }\r\n\r\n    > .fa-circle{\r\n      margin-top: 3px;\r\n    }\r\n  }\r\n\r\n  > li > .nav-link > .label {\